<template>
    <v-dialog
        v-model="dialog"
        persistent
        width="500"
    >
        <v-card>
            <v-card-text class="pt-3 pb-0">
                <v-col
                    align="start"
                    justify="center"
                >
                    {{title}}
                </v-col>
            </v-card-text>

            <v-card-title class="mb-3">
                <v-col
                    align="center"
                    justify="center"
                >
                    {{text}}
                </v-col>
            </v-card-title>

            <v-divider></v-divider>

            <v-card-actions class="pa-0">
                <v-col
                    align="center"
                    justify="center"
                    class="pa-0"
                >
                    <v-btn
                        width="100%"
                        height="100%"
                        class="pa-4"
                        color="primary"
                        @click.stop="ClosePopup(this)"
                    >
                        확인
                    </v-btn>
                </v-col>
            </v-card-actions>
        </v-card>
    </v-dialog>
</template>

<script>
export default {
    name: 'Dialog',
    props:['title','text','dialog'],
    data: () => ({
    }),
    computed: {
    },
    created(){
    },
    mounted(){
    },
    methods:{
        ClosePopup(e) {
            this.$emit('close-dialog', e);
        }
    }
}
</script>

