<template>
    <v-col class="fill-height">
        <v-row class="pl-10 pr-10 play_contents">
            <v-col cols="12" class="exercise" align-self="start">
                <v-row align="center">
                    <v-col cols="5" align="start">
                        {{exerciseName}}
                    </v-col>
                    <v-col cols="7" align="center" v-if="GET_PROGRAM_PLAY_EXERCISE_MODE === 2">
                        <v-row justify="center" class="exercise_motion_text" :style="common_check_motion(motionCamera_playZone.value)">
                            {{motionCamera_playZone.message}}
                        </v-row>
                    </v-col>
                </v-row>
            </v-col>
            <v-col cols="12" align-self="center">
                <v-row align="center">
                    <v-col cols="5" align="center">
                        <span v-if="GET_PROGRAM_PLAY_EXERCISE_MODE === 1" :class="(GET_PROGRAM_PLAY_START_TIME < 4)?'color_second':'white_second'" class="mr-10">{{GET_PROGRAM_PLAY_START_TIME}}</span>
                        <span v-if="GET_PROGRAM_PLAY_EXERCISE_MODE === 2" class="exercise_count"> {{GET_PROGRAM_PLAY_PLAY_ITEMS.exerciseCount}} / {{GET_PROGRAM_PLAY_GOAL_COUNT}} </span>
                    </v-col>
                    <v-col cols="7" align="center">
                        <video 
                            v-if="video_ex" 
                            width="100%" 
                            autoplay
                            loop 
                            :muted="false" 
                            id="exerciseVideo"
                            @click="Stop(this)"
                        >
                            <source :src="video_ex" type="video/mp4" />
                            <track v-if="track_ex" :src="track_ex" kind="subtitles" srclang="kr" default>
                        </video>
                        <img v-else :src="image" height="450">
                    </v-col>
                </v-row>
            </v-col>
        </v-row>
        <play-program-items-text-page
            v-if="!exerciseStart"
            :textPageItem="textPageItem"
            v-on:close-text="CloseText()"
        />
    </v-col>
</template>

<script>
import { mapGetters } from 'vuex'
import common from '@/mixins/common.js'

export default {
    name: 'Start',
    props:['motionCamera_playZone'],
    mixins:[ common ],
    data:()=>({
        exerciseStart: false,
        textPageItem: '',
        exerciseName: '',
        image: '',
        video_ex: null,
        track_ex: null,
    }),
    computed:{
        ...mapGetters([
            'GET_MENU_SELECT_PROGRAM',
            'GET_PROGRAM_PLAY_EXERCISE_MODE',
            'GET_PROGRAM_PLAY_START_TIME',
            'GET_PROGRAM_PLAY_CURRENT_PART',
            'GET_PROGRAM_PLAY_PAUSE',
            'GET_PROGRAM_PLAY_GOAL_COUNT',
            'GET_PROGRAM_PLAY_PLAY_ITEMS',
        ]),
    },
    watch:{
        GET_PROGRAM_PLAY_CURRENT_PART(){
            this.EditStartItems()
        },
        GET_PROGRAM_PLAY_PAUSE(value){
            if(value){
                document.getElementById("exerciseVideo").pause()
            }else{
                document.getElementById("exerciseVideo").play()
            }
        },
        GET_PROGRAM_PLAY_START_TIME(value){
            this.common_audio_start(value)
        }
    },
    mounted() {
        this.EditStartItems()
    },
    destroyed(){
        this.ResetItem()
    },
    methods: {
        CloseText(){
            this.exerciseStart = true
        },
        EditStartItems(){
            let index         = this.GET_PROGRAM_PLAY_CURRENT_PART-1

            this.textPageItem = `${this.GET_MENU_SELECT_PROGRAM[index].name} 시작.`
            this.exerciseName = this.GET_MENU_SELECT_PROGRAM[index].name
            this.image        = this.GET_MENU_SELECT_PROGRAM[index].image
            if( this.GET_MENU_SELECT_PROGRAM[index].video_ex !== undefined ) this.video_ex   = this.GET_MENU_SELECT_PROGRAM[index].video_ex
            if( this.GET_MENU_SELECT_PROGRAM[index].track_ex !== undefined ) this.track_ex   = this.GET_MENU_SELECT_PROGRAM[index].track_ex
        },
        ResetItem(){
            this.nextExercise   = ''
            this.image          = null
            this.video_ex       = null
            this.track_ex       = null
        },
        Stop(e){
            this.$emit('stop', e)
        }
    },
}
</script>

<style scoped lang="scss">
.exercise {
    font-size: var(--font-size-normal);
    vertical-align: bottom;
}
.exercise_count {
  color: white;
  font-size: var(--font-size-m-large);
  font-style: italic;
}
.exercise_motion_text{
    font-size: var(--font-size);
    padding-top: 0.85rem;
    padding-bottom: 0.85rem;
}
</style>