<template>
    <v-col cols="10" align="center" class="fill-height">
        <v-row align="center" class="select_times">
            <v-col align="center">
                <v-col class="select_times_description mb-5">
                    <span>{{selectTimesItems.title}}</span> 수행횟수를 선택하세요.
                </v-col>
                <v-col>
                    <div class="select_times_select_area_up_line"></div>
                    <div class="select_times_select_area">
                        <v-btn-toggle
                            v-model="select"
                            tile
                            color="deep-purple accent-3"
                            group
                        >
                            <template v-for="i in selectTimesItems.exerciseTime">
                                <div :key="i">
                                    <v-btn 
                                        text
                                        height="100"
                                        class="select_times_select_btn"
                                    >
                                        {{i-1}}
                                    </v-btn>
                                </div>
                            </template>
                        </v-btn-toggle>
                    </div>
                </v-col>
                <v-col>
                    <v-btn 
                        color="#fff5a2"
                        width="300"
                        height="120" 
                        class="select_times_button"
                        @click="CloseDialog(this)"
                    >
                        확인
                    </v-btn>
                </v-col>
            </v-col>
        </v-row>
    </v-col>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'

export default {
    name: 'SelectTimes',
    props:['selectTimesItems'],
    data:()=>({
        select: 10
    }),
    computed:{
        ...mapGetters([
            'GET_PART_PLAY_PLAY_SET',
            'GET_PART_PLAY_PLAY_ITEMS',
        ])
    },
    mounted() {
    },
    methods: {
        ...mapMutations([
            'MU_PART_PLAY_PLAY_ITEMS_EXERCISE_COUNT',
            'MU_PART_PLAY_PLAY_SET',
        ]),
        SaveData(){
            // count 저장
            let count  = this.GET_PART_PLAY_PLAY_ITEMS.exerciseCount + this.select
            this.MU_PART_PLAY_PLAY_ITEMS_EXERCISE_COUNT(count)

            // play set 저장
            let playSet = this.GET_PART_PLAY_PLAY_SET + 1
            this.MU_PART_PLAY_PLAY_SET(playSet)
        },
        CloseDialog(e){
            this.$emit('close-dialog', e)
        }
    },
}
</script>

<style scoped lang="scss">
.select_times{
    height: 50%;

    .theme--light.v-btn--active:hover::before, .theme--light.v-btn--active::before {
        opacity: 0.8;
    }
    .v-btn:before {
        background-color: darkred;
    }

    .select_times_description{
        color: white;
        font-size: var(--font-size-medium);
    }
    .select_times_description span{
        color: var(--primary-main);
    }
    .select_times_button{
        margin-top: 5rem;
        font-size: var(--font-size-medium);
        font-weight: 500;
        color: #000 !important;
    }
    .select_times_button:active{
        background-color: red;
    }
    .select_times_select_area{
        display: flex;
        flex-direction: row;
        text-align: center;
        align-items: center;
        -webkit-overflow-scrolling: touch;
        width: 100%;
        height: 120px;
        overflow-x: auto;
        white-space: nowrap;
        background: linear-gradient(90deg, rgba(0,0,0,1) 0%, rgba(79,79,79,1) 50%, rgba(0,0,0,1) 100%);
    }
    .select_times_select_area_up_line{
        height: 5px;
        background: linear-gradient(90deg, rgba(0,0,0,1) 0%, rgba(115,115,115,1) 50%, rgba(0,0,0,1) 100%);
    }
    .select_times_select_btn{
        margin: 0 1rem;
        width: 8rem;
        font-size: var(--font-size-large);
        font-weight: 300;
        color: var(--primary-text);
    }
}
</style>