<template>
    <v-row class="pl-10 pr-10 pt-10">
        <v-row v-if="setItems" justify="start">
            <template v-for="i in setItems.set">
                <div :key="i">
                    <v-btn 
                        v-if="setItems.setCount >= i"
                        fab
                        class="count_btn" 
                        :style="SelectSet(i)"
                        :color="selectColor"
                    >
                        {{i}}
                    </v-btn>
                    <v-btn 
                        v-else
                        fab
                        class="count_btn"
                        outlined
                    >
                        {{i}}
                    </v-btn>
                    <v-icon dark v-if="i < setItems.set">
                        horizontal_rule
                    </v-icon>
                </div>
            </template>
        </v-row>
        <v-spacer/>
        <img src="@/assets/images/common/icon_bi.png" width="169" height="42">
    </v-row>
</template>
<script>
export default {
    name: 'Header-bi',
    props:['setItems'],
    data:()=>({
        selectColor:'',
    }),
    methods: {
        SelectSet (i) {
            let style = {}
            if(this.setItems.setCount === i){
                style = {
                    backgroundColor: '#fff5a2',
                    color: 'black'
                }
            }else if(this.setItems.setCount > i){
                style = {
                    backgroundColor: 'white',
                    color: 'black'
                }
            }else{
                this.selectColor = 'black'
            }
            return style
        },
    }
}
</script>
<style scoped>
.count_btn{
    font-size: var(--font-size);
    font-style: italic;
    color: white;
    padding-right: 0.25rem;
    width: 3rem;
    height: 3rem;
}
.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 1rem;
}

</style>