<template>
    <v-col class="fill-height">
        <v-row class="pl-10 pr-10 play_contents">
            <v-row align="center" class="set_end_wrapper">
                <v-col align="center">
                    <p class="set_end_set">{{GET_PROGRAM_PLAY_PLAY_SET-1}}세트 종료</p>
                    <div class="set_end_time_area">
                        <span class="set_end_time">{{GET_PROGRAM_PLAY_REST_TIME}}초 후</span> 
                        시작합니다.
                    </div>
                    <p class="set_end_description">편하게 휴식을 취하세요.</p>
                </v-col>
            </v-row>
        </v-row>
    </v-col>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
    name: 'SetEnd',
    data:()=>({
    }),
    computed:{
        ...mapGetters([
            'GET_PROGRAM_PLAY_PLAY_SET',
            'GET_PROGRAM_PLAY_REST_TIME',
        ])
    },
    watch:{
        GET_PROGRAM_PLAY_REST_TIME(value){
            if(value === 0) this.CloseSetEnd(this)
        },
    },
    mounted() {
    },
    destroyed() {
        this.CloseSetEnd(this)
    },
    methods: {
        CloseSetEnd(e){
            this.$emit('close-end', e)
        }
    },
}
</script>

<style scoped lang="scss">
.set_end_wrapper{
    height: 80%;
}
.set_end_set{
    font-size: var(--font-size-medium);
}
.set_end_time_area{
    font-size: var(--font-size-title);
    .set_end_time{
        color: var(--primary-main);
    }
}
.set_end_description{
    font-size: var(--font-size);
    color: #777;
}
</style>