<template>
    <div class="menu-item" :class="type" @click="onClickItem()">
        <img :src="icon"><br/>
        {{text}}
    </div>
</template>
<script>
export default {
    data:()=>({
    }),
    props: [
        'index',
        'icon',
        'text',
        'link',
        'type'
    ],
    methods: {
        onClickItem() {
            this.$router.push(this.link)
        }
    }
}
</script>
