<template>
    <v-col class="fill-height">
        <layout-header-bi-x/>
        <v-row 
            v-if="exerciseItemsOn" 
            class="mt-5 exercise_items_area"
            align="center" 
        >
            <play-program-items-rest
                v-if="!exerciseOn && !setEndOn"
                v-on:stop="Pause()"
            />
            <play-program-items-start
                :motionCamera_playZone="motionCamera_playZone"
                v-if="exerciseOn && !setEndOn"
                v-on:stop="Pause()"
            />
            <play-program-items-set-end
                v-if="!exerciseOn && setEndOn"
                v-on:close-end="CloseSetEnd()"
            />
            <v-col cols="12" align-self="end">
                <play-program-items-status-bar
                    :status="pauseOn"
                    v-on:stop="Pause()"
                />
            </v-col>
        </v-row>
        <play-stop
            v-if="pauseOn"
            :dialog="pauseOn"
            :stopItems="stopItems"
            v-on:close-pause="ClosePause()"
        />
        <play-program-items-next-exercise
            v-if="selectSetOn"
            :dialog="selectSetOn"
            v-on:close-dialog="CloseDialog()"
        />
    </v-col>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'
import timer_program from '@/mixins/timer_program.js'
import common from '@/mixins/common.js'
import exercise from '@/mixins/exercise.js'

export default {
    name: 'ProgramExercise',
    props:['motionCamera_playZone'],
    mixins:[ timer_program, exercise, common ],
    data:()=>({
        exerciseOn      : false,
        exerciseItemsOn : false,
        pauseOn         : false,
        selectSetOn     : false,
        stopItems       : {},
        setEndOn        : false ,
        setEndOnItems   : {} ,
    }),
    computed:{
        ...mapGetters([
            'GET_MENU_SELECT_MENU',
            'GET_MENU_SELECT_EXERCISE',
            'GET_MENU_SELECT_PROGRAM',
            'GET_PROGRAM_PLAY_EXERCISE_SET_START',
            'GET_PROGRAM_PLAY_EXERCISE',
            'GET_PROGRAM_PLAY_PLAY_SET',
            'GET_PROGRAM_PLAY_PAUSE_TIME',
            'GET_PROGRAM_PLAY_PAUSE',
        ]),
    },
    watch:{
        GET_PROGRAM_PLAY_EXERCISE(value){
            this.exerciseOn = value
            // console.log('exercise', value)
            // if(!value) this.CloseSetEnd()
        },
        GET_PROGRAM_PLAY_PLAY_SET(value){
            if(value > 1){
                this.ResetItems()
                this.exerciseItemsOn = true
                this.setEndOn = true
                this.SetStartTime()
            }
        },
        GET_PROGRAM_PLAY_PAUSE_TIME(){
            this.EditStopItems()
        },
        GET_PROGRAM_PLAY_PAUSE(value){
            if(!value){
                this.pauseOn = false
            }
        },
        GET_PROGRAM_PLAY_EXERCISE_SET_START(value){
            if(value) this.CloseSetEnd()
        }
    },
    mounted() {
        this.ResetItems()
        this.ConfirmExercise()
        // set 선택
        // this.selectSetOn = true
        
    },
    destroyed() {
        this.timer_program_stop()
        this.$emit('close-on',this)
    },
    methods: {
        ...mapMutations([
            'MU_PROGRAM_PLAY_PAUSE',
            'MU_PROGRAM_PLAY_PLAY_ITEMS_SET_START_TIME',
            'MU_PROGRAM_PLAY_TOTAL_PART',
        ]),
        ConfirmExercise(){
            this.EditProgramInfo()
            this.SetStartTime()
            this.StartPart()
        },
        EditProgramInfo(){
            // program part 값 적용
            let programLength = this.GET_MENU_SELECT_PROGRAM.length
            this.MU_PROGRAM_PLAY_TOTAL_PART(programLength)
        },
        SetStartTime(){
            // setStartTime
            const startTime = this.common_set_start_time()
            this.MU_PROGRAM_PLAY_PLAY_ITEMS_SET_START_TIME(startTime)
        },
        StartPart(){
            this.ResetItems()
            this.exerciseItemsOn = true
            this.timer_program_start()
        },
        ResetItems(){
            this.exerciseItemsOn    = false
            this.pauseOn            = false
            this.setEndOn           = false 
            this.selectSetOn        = false
        },
        Pause(){
            this.pauseOn = true
            this.EditStopItems()
            this.MU_PROGRAM_PLAY_PAUSE(true)
        },
        EditStopItems(){
            let button

            this.stopItems = {
                naviItems:{
                    title: this.GET_MENU_SELECT_EXERCISE.name,
                    buttonGroup:[
                        {
                            text:'운동 계속하기',
                            cancel: true,
                        }
                    ]
                },
                pauseItems:{
                    title: '일시정지',
                    time: this.GET_PROGRAM_PLAY_PAUSE_TIME
                }
            }
            
            // 휴식중 예외처리
            if(this.GET_PROGRAM_PLAY_EXERCISE){
                button = { text:'셋트 종료', setEnd: true }
            }else{
                button = { text:'나가기', link: '/user/main'}
            }

            this.stopItems.naviItems.buttonGroup.push(button)
        },
        ClosePause(){
            this.MU_PROGRAM_PLAY_PAUSE(false)
        },
        CloseSetEnd(){
            this.setEndOn = false
        },
        CloseDialog(){
            this.ConfirmExercise()
        }
    },
}
</script>

<style scoped lang="scss">
.exercise_items_area{
    height: 60vh;
}
.exercise_sub_title{
    font-size: var(--font-size-normal);
}
@media screen and (min-width: 1600px) { 
    .exercise_items_area{
        height: 69vh;
    }
}
</style>