<template>
    <v-col class="fill-height">
        <v-row class="pl-10 pr-10 play_contents">
            <v-col cols="12" align-self="start">
                <v-row align="end" class="rest_next_exercise">
                    <span class="label">다음 운동</span>
                    {{nextExercise}}
                </v-row>
            </v-col>
            <v-col cols="12" align-self="center">
                <v-row align="center">
                    <v-col cols="5" align="center">
                        <span :class="(GET_PROGRAM_PLAY_REST_TIME < 4)?'color_second':'white_second'">
                            {{GET_PROGRAM_PLAY_REST_TIME}}
                        </span>
                        <span class="rest_text">초 후 시작</span>
                    </v-col>
                    <v-col cols="7" align="center">
                        <video 
                            v-if="video_rest" 
                            width="100%" 
                            autoplay 
                            loop 
                            :muted="false" 
                            id="exerciseVideo"
                            @click="Stop(this)"
                        >
                            <source :src="video_rest" type="video/mp4" />
                            <track v-if="track_rest" :src="track_rest" kind="subtitles" srclang="kr" default>
                            <audio v-if="audio_rest" autoplay :src="audio_rest" id="exerciseAudio"></audio>
                        </video>
                        <img v-else :src="image" height="450">
                    </v-col>
                </v-row>
            </v-col>
        </v-row>
    </v-col>
</template>

<script>
import { mapGetters } from 'vuex'
import common from '@/mixins/common.js'

export default {
    name: 'Rest',
    mixins: [ common ],
    data:()=>({
        nextExercise: '',
        image: null,
        video_rest: null,
        track_rest: null,
        audio_rest: null,
    }),
    computed:{
        ...mapGetters([
            'GET_MENU_SELECT_PROGRAM',
            'GET_PROGRAM_PLAY_REST_TIME',
            'GET_PROGRAM_PLAY_CURRENT_PART',
            'GET_PROGRAM_PLAY_PAUSE',
        ]),
    },
    watch:{
        GET_PROGRAM_PLAY_PAUSE(value){
            if(value){
                document.getElementById("exerciseVideo").pause()
                document.getElementById("exerciseAudio").pause()
            }else{
                document.getElementById("exerciseVideo").play()
                document.getElementById("exerciseAudio").play()
            }
        },
        GET_PROGRAM_PLAY_REST_TIME(value){
            this.common_audio_start(value)
        }
    },
    mounted() {
        this.EditRestItems()
    },
    destroyed(){
        this.ResetItem()
    },
    methods: {
        EditRestItems(){
            let index         = this.GET_PROGRAM_PLAY_CURRENT_PART
            
            this.nextExercise = this.GET_MENU_SELECT_PROGRAM[index].name
            this.image        = this.GET_MENU_SELECT_PROGRAM[index].image
            if( this.GET_MENU_SELECT_PROGRAM[index].video_rest !== undefined ) this.video_rest   = this.GET_MENU_SELECT_PROGRAM[index].video_rest
            if( this.GET_MENU_SELECT_PROGRAM[index].track_rest !== undefined ) this.track_rest   = this.GET_MENU_SELECT_PROGRAM[index].track_rest
            if( this.GET_MENU_SELECT_PROGRAM[index].audio_rest !== undefined ) this.audio_rest   = this.GET_MENU_SELECT_PROGRAM[index].audio_rest
        },
        ResetItem(){
            this.nextExercise   = ''
            this.image          = null
            this.video_rest     = null
            this.track_rest     = null
            this.audio_rest     = null
        },
        Stop(e){
            this.$emit('stop', e)
        }
    },
}
</script>

<style scoped lang="scss">
.rest_text {
    color: white;
    font-size: var(--font-size-normal);
    font-style: italic;
}

.rest_next_exercise {
    font-size: var(--font-size-normal);

    .label {
        font-size: var(--font-size);
        font-weight: lighter;
        margin-bottom:0.8rem;
        margin-right: 1rem;
    }
}
</style>