import { mapGetters, mapMutations } from 'vuex'

export default {
    data: () => ({
        timer_part: 0,
        timer_part_exercise:'',
        timer_part_exercise_minute: 0,
        timer_part_exercise_secode: 0,
        timer_part_exercise_time_mode_secode: 0,
        timer_part_rest_secode: 0,
        timer_part_delay: 2,
        timer_part_pause_minute: 0,
        timer_part_pause_secode: 0,
    }),
    computed: {
        ...mapGetters([
            'GET_PART_PLAY_EXERCISE_DELAY',
            'GET_PART_PLAY_EXERCISE_MODE',
            'GET_PART_PLAY_EXERCISE',
            'GET_PART_PLAY_PAUSE',
            'GET_PART_PLAY_TOTAL_EXERCISE_TIME',
            'GET_PART_PLAY_TOTAL_REST_TIME',
            'GET_PART_PLAY_REST_SET',
        ]),
    },
    watch: {
        GET_PART_PLAY_EXERCISE(value){
            if(value) this.timer_part_rest_set()
        },
        GET_PART_PLAY_PAUSE(value){
            if(!value) {
                this.timer_part_pause_minute = 0
                this.timer_part_pause_secode = 0
            }
        },
        timer_part_exercise_secode(value){
            if(value > -1){
                if(value === 60){
                    this.timer_part_exercise_secode = 0
                    this.timer_part_exercise_minute += 1
                }
                let minute = (this.timer_part_exercise_minute < 10)? '0'+this.timer_part_exercise_minute : this.timer_part_exercise_minute
                let second = (this.timer_part_exercise_secode < 10)? '0'+this.timer_part_exercise_secode : this.timer_part_exercise_secode
                this.timer_part_exercise = minute + ':' + second
            }
        },
        timer_part_rest_secode(value){
            this.MU_PART_PLAY_REST_TIME(value)
            if(value === 0){
                this.MU_PART_PLAY_EXERCISE(true)
                this.timer_part_rest_set()
            }
        },
        timer_part_exercise(value){
            this.MU_PART_PLAY_EXERCISE_TIME(value)
        },
        timer_part(){
            // PART
            if(this.GET_PART_PLAY_EXERCISE_DELAY){
                this.timer_part_delay -= 1
                if(this.timer_part_delay === 0){
                    this.timer_part_delay = 2
                    this.MU_PART_PLAY_EXERCISE_DELAY(false)
                }
            }else{
                if(this.GET_PART_PLAY_EXERCISE){
                    if(this.GET_PART_PLAY_EXERCISE_MODE === 1 && this.timer_part_exercise_time_mode_secode > 0){
                        this.timer_part_exercise_time_mode_secode -= 1
                        this.MU_PART_PLAY_EXERCISE_TIME_MODE_TIME(this.timer_part_exercise_time_mode_secode)
                    }
                    this.timer_part_exercise_secode += 1
                    let totalExerciseTime = this.GET_PART_PLAY_TOTAL_EXERCISE_TIME + 1
                    this.MU_PART_PLAY_TOTAL_EXERCISE_TIME(totalExerciseTime)
                }else{
                    this.timer_part_rest_secode -= 1
                    let totalRestTime = this.GET_PART_PLAY_TOTAL_REST_TIME + 1
                    this.MU_PART_PLAY_TOTAL_REST_TIME(totalRestTime)
                }
            }
        },
        timer_part_pause_secode(value){
            if(this.GET_PART_PLAY_PAUSE){
                if(value === 60){
                    this.timer_part_pause_secode = 0
                    this.timer_part_pause_minute += 1
                }
                let minute = (this.timer_part_pause_minute < 10)? '0'+this.timer_part_pause_minute : this.timer_part_pause_minute
                let second = (this.timer_part_pause_secode < 10)? '0'+this.timer_part_pause_secode : this.timer_part_pause_secode
                let timer_part_pause = minute + ':' + second

                this.MU_PART_PLAY_PAUSE_TIME(timer_part_pause)
            }
        }
    },
    created(){
        // Time 모드 시간 Reset적용
        this.timer_part_exercise_time_mode_secode = 25
    },
    mounted(){
        this.timer_part_rest_set()
    },
    updated(){
    },
    destroyed(){
    },
    methods:{
        ...mapMutations([
            'MU_PART_PLAY_EXERCISE_DELAY',
            'MU_PART_PLAY_EXERCISE',
            'MU_PART_PLAY_EXERCISE_TIME',
            'MU_PART_PLAY_REST_TIME',
            'MU_PART_PLAY_TOTAL_EXERCISE_TIME',
            'MU_PART_PLAY_TOTAL_REST_TIME',
            'MU_PART_PLAY_PAUSE_TIME',
            'MU_PART_PLAY_EXERCISE_TIME_MODE_TIME',
        ]),
        timer_part_rest_set(){
            this.timer_part_rest_secode = this.GET_PART_PLAY_REST_SET
        },
        timer_part_start() {
            this.interval_Timer = setInterval(() => {
                if(this.GET_PART_PLAY_PAUSE){
                    this.timer_part_pause_secode += 1
                }else{
                    this.timer_part += 1
                }
            }, 1000)
        },
        timer_part_stop(){
            clearInterval(this.interval_Timer)
        },
    }
}
