<template>
    <v-dialog
        v-model="dialog"
        persistent
        width="100%"
    >
        <v-col align="center" class="fill-height text_page_area">
            <v-row align="center" class="text_wrapper ml-10">
                <v-col align="center">
                    <p class="text_title">{{textPageItem}}</p>
                </v-col>
            </v-row>
        </v-col>
    </v-dialog>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'

export default {
    name: 'TextPage',
    props:['textPageItem'],
    data:()=>({
        dialog: false
    }),
    computed:{
        ...mapGetters([
            'GET_PROGRAM_PLAY_EXERCISE_DELAY'
        ])
    },
    watch:{
        GET_PROGRAM_PLAY_EXERCISE_DELAY(value){
            if(value){
                this.dialog = true
            }else{
                this.closePopup(this)
            }
        }
    },
    mounted() {
        this.MU_PROGRAM_PLAY_EXERCISE_DELAY(true)
    },
    methods: {
        ...mapMutations([
            'MU_PROGRAM_PLAY_EXERCISE_DELAY'
        ]),
        closePopup(e){
            this.dialog = false
            this.$emit('close-text', e);
        }
    },
}
</script>

<style scoped lang="scss">
.text_page_area{
    background-color: rgba(0, 0, 0, 0.8);
    
    .text_wrapper{
        height: 100vh;
        
        .text_title{
            color: white;
            font-size: var(--font-size-large);
        }
    }
}

</style>