<template>
    <v-navigation-drawer
        fixed
        permanent
        right
        color="#323232"
        width="380"
        clipped
        :app="appOn"
    >
        <v-list dense>
            <v-list-item>
                <v-col align="center" class="mt-10">
                    <img src="@/assets/images/common/icon_bi.png">
                </v-col>
            </v-list-item>
            <v-col align="center" class="right_navi_title pl-2 pr-2">
                <p v-if="naviItems.nextExercise" class="right_navi_sub_title">{{naviItems.nextExercise}}</p>
                <span>{{naviItems.title}}</span>
            </v-col>
            <v-col v-if="naviItems.buttonGroup" align="center" class="right_navi_button_area">
                <template v-for="(button,i) in naviItems.buttonGroup" >
                    <v-btn 
                        :key="i" 
                        color="#fff5a2"
                        width="300"
                        height="120" 
                        class="right_navi_button mt-6"
                        @click="ClickButton(button)"
                    >
                        {{button.text}}
                    </v-btn>
                </template>
            </v-col>
        </v-list>
    </v-navigation-drawer>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'

export default {
    name: 'rightNavi',
    props:['naviItems'],
    data:()=>({
        appOn: true
    }),
    computed:{
        ...mapGetters([
            // PART
            'GET_PART_PLAY_PAUSE',
            'GET_PART_PLAY_PLAY_SET',
            // PROGRAM
            'GET_PROGRAM_PLAY_PAUSE',
            'GET_PROGRAM_PLAY_PLAY_SET',
        ]),
    },
    watch:{
    },
    mounted() {
        this.ComfirmPause()
    },
    methods: {
        ...mapMutations([
            // PART
            'MU_PART_PLAY_PAUSE',
            'MU_PART_PLAY_PLAY_SET',

            // PROGRAM
            'MU_PROGRAM_PLAY_PAUSE',
            'MU_PROGRAM_PLAY_PLAY_SET',
            'MU_PROGRAM_PLAY_CURRENT_PART',
            'MU_PROGRAM_PLAY_EXERCISE',
        ]),
        ClickButton(button){
            if(button.link){
                this.$router.push(button.link)
            }else if(button.cancel){
                if(this.GET_PART_PLAY_PAUSE){
                    this.MU_PART_PLAY_PAUSE(false)
                }
                if(this.GET_PROGRAM_PLAY_PAUSE){
                    this.MU_PROGRAM_PLAY_PAUSE(false)
                }
            }else if(button.setEnd){
                // PART PAUSE 체크
                if(this.GET_PART_PLAY_PAUSE){

                    // PART play set 저장
                    let playSet = this.GET_PART_PLAY_PLAY_SET + 1
                    this.MU_PART_PLAY_PLAY_SET(playSet)
                    
                    // PART pause false
                    this.MU_PART_PLAY_PAUSE(false)
                }

                // PROGRAM PAUSE 체크
                if(this.GET_PROGRAM_PLAY_PAUSE){
                    // PROGRAM set 저장
                    let playSet = this.GET_PROGRAM_PLAY_PLAY_SET + 1
                    this.MU_PROGRAM_PLAY_PLAY_SET(playSet)

                    // PROGRAM pause false
                    this.MU_PROGRAM_PLAY_PAUSE(false)
                    // PART reset
                    this.MU_PROGRAM_PLAY_CURRENT_PART(0)
                    // REST TIME start
                    this.MU_PROGRAM_PLAY_EXERCISE(false)
                }
            }
        },
        ComfirmPause(){
            if(this.GET_PART_PLAY_PAUSE || this.GET_PROGRAM_PLAY_PAUSE){
                this.appOn = false
            }else{
                this.appOn = true
            }
        }
    },
}
</script>

<style scoped lang="scss">
.right_navi_title{
    margin-top: 3rem;
    font-size: var(--font-size-medium);
    color: var(--primary-text);
    font-weight: bold;
}
.right_navi_button_area{
    position: absolute;
    bottom: 2rem;
}
.right_navi_button{
    padding-top: 1rem;
    font-size: var(--font-size-medium);
    font-weight: 500;
}
.right_navi_sub_title{
    font-size: var(--font-size);
    font-weight: 400;
}

</style>