<template>
    <v-col cols="10" align="center" class="fill-height">
        <v-row align="center" class="select_number">
            <v-col align="center">
                <v-col>
                    <div class="select_number_select_area_up_line"></div>
                    <div class="select_number_select_area">
                        <v-btn-toggle
                            v-model="select"
                            tile
                            color="deep-purple accent-3"
                            group
                        >
                            <template v-for="i in selectItems.exerciseCount">
                                <div :key="i" :ref="i-1">
                                    <v-btn 
                                        :disabled="(i-1 === 0)? true : false"
                                        text
                                        height="100"
                                        class="select_number_select_btn"
                                    >
                                        {{i-1}}
                                    </v-btn>
                                </div>
                            </template>
                        </v-btn-toggle>
                    </div>
                </v-col>
                <v-col>
                    <v-btn 
                        color="#fff5a2"
                        width="300"
                        height="120" 
                        class="select_number_button"
                        @click="CloseDialog(this)"
                    >
                        확인
                    </v-btn>
                </v-col>
            </v-col>
        </v-row>
    </v-col>
</template>

<script>
import { mapMutations } from 'vuex'
import common from '@/mixins/common.js'

export default {
    name: 'SelectSet',
    mixins: [common],
    props:['selectItems'],
    data:()=>({
        select: 1,
    }),
    computed:{
    },
    watch:{
        select(value){
            this.common_select_scroll(value)
        }
    },
    mounted(){
    },
    methods: {
        ...mapMutations([
            'MU_PROGRAM_PLAY_TOTAL_SET',
            'MU_PROGRAM_PLAY_PLAY_ITEMS_TOTAL_SET',
        ]),
        SaveData(){
            // set 저장
            this.MU_PROGRAM_PLAY_TOTAL_SET(this.select)
            this.MU_PROGRAM_PLAY_PLAY_ITEMS_TOTAL_SET(this.select)
        },
        CloseDialog(e){
            this.SaveData()
            this.$emit('close-dialog', e)
        }
    },
}
</script>

<style scoped lang="scss">
.select_number{
    height: 50%;

    .theme--light.v-btn--active:hover::before, .theme--light.v-btn--active::before {
        opacity: 0.8;
    }
    .v-btn:before {
        background-color: darkred;
    }

    .select_number_description{
        color: white;
        font-size: var(--font-size-medium);
    }
    .select_number_description span{
        color: var(--primary-main);
    }
    .select_number_button{
        margin-top: 5rem;
        font-size: var(--font-size-medium);
        font-weight: 500;
        color: #000 !important;
    }
    .select_number_button:active{
        background-color: red;
    }
    .select_number_select_area{
        display: flex;
        flex-direction: row;
        text-align: center;
        align-items: center;
        justify-content: center;
        touch-action: pan-x;
        -webkit-overflow-scrolling: touch;
        width: 100%;
        height: 120px;
        overflow-x: auto;
        white-space: nowrap;
        background: linear-gradient(90deg, rgba(0,0,0,1) 0%, rgba(79,79,79,1) 50%, rgba(0,0,0,1) 100%);
    }
    .select_number_select_area_up_line{
        height: 5px;
        background: linear-gradient(90deg, rgba(0,0,0,1) 0%, rgba(115,115,115,1) 50%, rgba(0,0,0,1) 100%);
    }
    .select_number_select_btn{
        margin: 0 1rem;
        width: 8rem;
        font-size: var(--font-size-large);
        font-weight: 300;
        color: var(--primary-text);
    }
}
</style>