<template>
    <v-row class="footer pl-10 pr-10 pb-8" justify="center" align="center">
        <!--서초구 납품 운동남12 안보이게 처리-->
        <div v-if="site !== '1'" class="button-left">
            {{ this.GET_APP_MEMBER_INFO.memName }} <span>님</span>
        </div>
        <v-spacer/>
        <v-btn @click="this.ACT_APP_LOGOUT" text dark class="button-right pa-0" to="/user/main">
            나가기 >
        </v-btn>
    </v-row>
</template>
<script>
import { mapGetters, mapActions } from 'vuex'
export default {
    name: 'Footer',
    data:()=>({
        site: process.env.VUE_APP_SITE
    }),
    computed: {
        ...mapGetters([
            'GET_APP_MEMBER_INFO',
        ])
    },
    mounted(){
        if(this.GET_APP_MEMBER_INFO === null){
            this.$router.push('/')
        }else{
            return this.GET_APP_MEMBER_INFO.memName
        }
    },
    methods: {
        ...mapActions([
            'ACT_APP_LOGOUT',
        ]),
    }
}
</script>
<style scoped>
    .footer {
        position: absolute;
        width: 100%;
        color: white;
        bottom: 0;
    }
    .button-left {
        font-size: 36px;
    }
    .button-left span{
        font-size: 24px;
    }
    .button-right {
        font-size: 36px;
    }
</style>