const { NFC, TAG_ISO_14443_3, TAG_ISO_14443_4 } = require('nfc-pcsc')

class CardReader {
    constructor(func) {
        this.dataReceived = func;
        this.isConnectReader = false;

        const nfc = new NFC(); // const nfc = new NFC(minilogger); // optionally you can pass logger to see internal debug logs
        let readers = [];

        nfc.on('reader', async reader => {

            readers.push(reader);
            reader.aid = 'F222222222'
            this.isConnectReader = true;

            reader.on('card', async card => {

                // standard nfc tags like Mifare
                if (card.type === TAG_ISO_14443_3) {
                    // const uid = card.uid;
                    console.log(`[hb] card detected`, card);
                    this.readCard(card.uid.toUpperCase())
                }
                // // Android HCE
                else if (card.type === TAG_ISO_14443_4) {
                    // process raw Buffer data
                    const data = card.data.toString('utf8');
                    card.data.toString('utf8');
                    console.log(data)
                }

                // try {
                //     const data = await reader.read(4, 16); // await reader.read(4, 16, 16); for Mifare Classic cards

                //     // const payload = data.readInt16BE();
                //     data.readInt16BE();
                // } catch (err) {
                //     console.error(err)
                // }

                // try {
                //     const data = Buffer.allocUnsafe(16);
                //     data.fill(0);
                //     const randomNumber = Math.round(Math.random() * 1000);
                //     data.writeInt16BE(randomNumber);

                //     await reader.write(4, data); // await reader.write(4, data, 16); for Mifare Classic cards
                // } catch (err) {
                //     console.error(err)
                // }
            });

            reader.on('error', () => {
                this.isConnectReader = false;
            });

            // reader.on('error', err => {
            //     this.isConnectReader = false;
            // });

            reader.on('end', () => {
                delete readers[readers.indexOf(reader)];
                this.isConnectReader = false;
            });
        });

        nfc.on('error', () => this.isConnectReader = false)
        // nfc.on('error', err => {
        //     this.isConnectReader = false;
        // });
    }

    readCard(readData) {
        if(typeof this.dataReceived == 'function') {
            this.dataReceived(readData);
        }
    }
}

export default CardReader