<template>
    <v-col align="center" justify="center">
        <v-col align="start">
            <v-col class="list_title">
                {{ title }}
                <div v-if="switchItems && switchItems.length > 1" class="switch_wrapper list_title">
                    <span
                        v-for="(select, i) in switchItems" 
                        :key="i"
                        class="switch_item" 
                        :class="classForSwitch(i)"
                        @click="SelectSwitch(select)"
                    >
                        {{ select.text }}
                    </span>
                </div>
            </v-col>
            <v-col v-if="description" class="list_description">
                {{ description }}
            </v-col>
        </v-col>
        <v-row 
            v-if="switchItems" 
            v-model="selectedTab" 
            class="tab_wrapper"
        >
            <span 
                v-for="(tab, tabIndex) in tabItems" 
                :key="tabIndex"
            >
                <span 
                    class="tab_item" 
                    :class="classForTab(tabIndex)"
                    @click="SelectTab(tab)"
                > {{ tab.text }} </span>
                <span 
                    v-if="tabIndex < (tabItems.length - 1)"
                    class="ml-1 mr-3"
                > | </span>
            </span>
        </v-row>
        <div class="thumbnail_wrapper mt-2">
            <template v-for="(thumbnamil, i) in thumbnailSeachList">
                <div 
                    class="thumbnail_card" 
                    :key="i" 
                    @click="onClickCard(thumbnamil)"
                >
                    <v-img :src="thumbnamil.image" class="thumbnail mb-5">
                        <div v-if="envCheck === 'true'" class="fill-height">
                            <v-row class="fill-height thumbnail_check justify-space-between" v-if="thumbnamil.check">
                                <v-btn fab small color="primary" class="ml-5 mt-5">
                                    <v-icon color="black">check</v-icon>
                                </v-btn>
                                <v-btn fab small color="primary" class="mr-5 mt-5" v-if="thumbnamil.motion">
                                    <v-icon color="black">accessibility_new</v-icon>
                                </v-btn>
                            </v-row>
                            <v-row class="fill-height" justify="end" v-if="!thumbnamil.check && thumbnamil.motion">
                                <v-btn fab small color="primary" class="mr-5 mt-5">
                                    <v-icon color="black">accessibility_new</v-icon>
                                </v-btn>
                            </v-row>
                        </div>
                        <div v-else>
                            <v-row class="fill-height" justify="end" v-if="thumbnamil.motion">
                                <v-btn fab small color="primary" class="mr-5 mt-5">
                                    <v-icon color="black">accessibility_new</v-icon>
                                </v-btn>
                            </v-row>
                            <v-row class="fill-height" justify="end" v-if="thumbnamil.skeleton">
                                <v-btn fab small color="primary" class="mr-5 mt-5">
                                    <v-icon color="black">share</v-icon>
                                </v-btn>
                            </v-row>
                        </div>
                    </v-img>
                    <div class="thumbnail_title">
                        {{ thumbnamil.name }}
                    </div>
                </div>
            </template>
        </div>
    </v-col>
</template>

<script>
import { mapActions, mapGetters, mapMutations } from 'vuex'

export default {
    name: 'MenuList',
    props: [
        'title',
        'description',
        'switchItems',
        'thumbnailList'
    ],
    data:()=>({
        switchOn: 0,
        selectedTab: 0,
        thumbnailItems: [],
        envCheck: process.env.VUE_APP_EXERCISE_CHECK
    }),
    computed: {
        ...mapGetters([
            'GET_APP_MEMBER_INFO'
        ]),
        thumbnailSeachList(){
            if(this.selectedTab === 0) return this.thumbnailItems
            return this.thumbnailItems.filter(list => list.tabType === this.selectedTab)
        },
        tabItems(){
            const tab = this.switchItems.find(switchItem => switchItem.switchType === this.switchOn)
            return tab.tabItems
        }
    },
    watch:{
        switchOn(){
            this.SwitchThumbnail()
        },
        thumbnailList(){
            this.SwitchThumbnail()
        }
    },
    mounted(){
        // 선택 Menu Vuex에 저장
        this.MU_MENU_SELECT_MENU(this.title)
        // Thumbnail switchItems 유무 확인
        this.SwitchThumbnail()
    },
    methods: {
        ...mapActions([
            'ACT_MENU_API_GET_PART_TOOL'
        ]),
        ...mapMutations([
            'MU_MENU_SELECT_MENU',
            'MU_MENU_SELECT_EXERCISE',
        ]),
        SwitchThumbnail(){
            if(this.switchItems !== undefined){
                this.thumbnailItems = this.thumbnailList.filter(list => list.switchType === this.switchOn)
            }else{
                this.thumbnailItems = this.thumbnailList
            }
        },
        classForSwitch (index) {
            return (this.switchOn === index) ? 'on' : 'off'
        },
        classForTab (index) {
            return (this.selectedTab === index) ? 'selected' : ''
        },
        async onClickCard (item) {
            // 선택된 item vuex 저장
            if(this.title === '프리웨이트'){
                let tool = await this.GetExerciseTool(item)
                if(tool.exTool.length > 0) item.exTool = tool.exTool[0]
            }
            await this.MU_MENU_SELECT_EXERCISE(item)
            this.$router.push(item.link)
        },
        async GetExerciseTool(item){
            // FreeWeight일 경우에만 해당
            let params = {
                exSortCode  : item.exSortCode,
                gender      : this.GET_APP_MEMBER_INFO.memGender,
                isZeroMass  : 0  // 무게제로여부 0 / 1
            }
            return await this.ACT_MENU_API_GET_PART_TOOL(params)
        },
        SelectSwitch(item){
            this.switchOn = item.switchType
            this.selectedTab = 0
        },
        SelectTab(item){
            this.selectedTab = item.tabType
        }
    }
}
</script>
<style scoped>
.list_title {
    font-size: 72px;
    font-weight: bold;
    padding-bottom: 0px;
    margin-bottom: 10px;
    display: flex;
}
.list_description {
    height: 54px;
    font-size: 36px;
    margin-bottom: 25px;
}
.switch_wrapper {
    margin-top: 15px;
    margin-bottom: 15px;
    margin-left: 58px;
    font-size: 36px;
    border-radius: 5px;
    background-color: white;        
}
.switch_item {
    display: inline-block;
    padding: 10px 20px;
    min-width: 220px;
    text-align: center;
    border-radius: 5px;
}
.switch_item.on {
    background-color: white;
    color: black;
    border: solid 2px white;
}
.switch_item.off {
    background-color: black;
    color: white;
    border: solid 2px;   
}

.tab_wrapper {
    font-size: var(--font-size-medium);
    height: 4rem;
    margin-bottom: 20px;
    
}
.tab_item {
    min-width: 100px;
}
.tab_item.selected {
    color: var(--primary-main);
}

.thumbnail {
    width: 320px;
    height: 180px;
}

.thumbnail_check{
    background-color: var(--primary-select-bg);
}

.thumbnail_wrapper {
    float: left;
    display: flex;
    flex-direction: row;
    /* -webkit-overflow-scrolling: touch; */
    width: 100%;
    padding-right: 1rem;
    height: 230px;
    white-space: nowrap;
    overflow-x: auto;
}
.thumbnail_card {
    margin-right: 20px;
    margin-bottom: 20px;
}
.thumbnail_title {
    width: 320px;
    font-size: var(--font-size-small);
    text-align: center;
}

</style>