<template>
    <v-row align="start" class="pt-6">
        <v-col cols="1" align="center">
            <v-btn v-if="statusBarItems.play" text dark class="play_button pa-0">
                <v-icon class="play_button">play_circle_outline</v-icon>
                <!-- <v-icon class="play_button">pause_circle_outline</v-icon> -->
                <!-- <img src="@/assets/images/icons/btn_play_normal.png"> -->
            </v-btn>
            <v-btn v-else text dark class="play_button pa-0">
                <v-icon class="play_button">pause_circle_outline</v-icon>
            </v-btn>
        </v-col>
        <v-col justify="center" align="start" class="mt-2">
            <span class="time ml-2"> {{statusBarItems.exerciseTime}} </span>
            <v-row justify="start" align="end">
                <v-card
                    class="d-flex time_persent_area"
                    color="black"
                    flat
                    tile
                    >
                    <v-card
                        v-for="(exercisePart,i) in exercisePartItems"
                        :key="i"
                        :color="exercisePart"
                        class="time_persent"
                        :class="((i+1) % statusBarItems.totalExercisePart === 0)? 'mr-5':''"
                        outlined
                        tile
                    >
                    </v-card>
                </v-card>
            </v-row>
        </v-col>
    </v-row>
</template>

<script>
export default {
    name: 'StatusBar',
    props: ['statusBarItems'],
    data:()=>({
        exercisePartItems: []
    }),
    watch:{
    },
    mounted() {
        this.Calculator()
    },
    methods: {
        Calculator(){
            const totalSet      = this.statusBarItems.totalSet
            const currentSet    = this.statusBarItems.currentSet
            const totalPart     = this.statusBarItems.totalExercisePart
            const currentPart   = this.statusBarItems.currentExercisePart
            let partGroup = []
            for(let n = 0; totalSet > n; n++){
                for(let i = 0; totalPart > i; i++){
                    if(currentPart-1 > i && currentSet === n+1){
                        partGroup.push('white')
                    }else if(currentPart-1 === i && currentSet === n+1){
                        partGroup.push('#fff5a2')
                    }else{
                        partGroup.push('grey')
                    }
                }
            }
            this.exercisePartItems = partGroup
        }
    },
    
}
</script>

<style scoped lang="scss">
.time{
    font-size: var(--font-size);
}
.time_persent_area{
    width: 100%;
    margin-top: 1rem;
    margin-left: 0.5rem;

    .time_persent{
        width: 32%;
        height: 0.85rem;
        margin-right: 0.2rem;
    }
}
.play_button{
    width: 100% !important;
    height: 100% !important;
    font-size: 6.5rem;
}
</style>