<template>
    <v-row align="start" class="pl-1 pr-5">
        <v-col cols="1" align="center">
            <v-btn 
                v-if="status" 
                text 
                dark 
                class="play_button pa-0"
            >
                <v-icon class="play_button">play_circle_outline</v-icon>
            </v-btn>
            <v-btn 
                v-else 
                text 
                dark 
                class="play_button pa-0"
                @click="Stop(this)"
            >
                <v-icon class="play_button">pause_circle_outline</v-icon>
            </v-btn>
        </v-col>
        <v-col justify="center" align="start" class="mt-2">
            <span class="time ml-2"> {{GET_PROGRAM_PLAY_EXERCISE_TIME}} </span>
            <v-row justify="start" align="end">
                <v-card
                    class="d-flex time_persent_area"
                    color="black"
                    flat
                    tile
                    >
                    <v-card
                        v-for="(exercisePart,i) in exercisePartItems"
                        :key="i"
                        :color="exercisePart"
                        class="time_persent"
                        :class="((i+1) % totalPart === 0)? 'mr-5':''"
                        outlined
                        tile
                    >
                    </v-card>
                </v-card>
            </v-row>
        </v-col>
    </v-row>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
    name: 'StatusBar',
    props: ['status'],
    data:()=>({
        totalPart: 0,
        exercisePartItems: []
    }),
    computed:{
        ...mapGetters([
            'GET_PROGRAM_PLAY_EXERCISE_TIME',
            'GET_PROGRAM_PLAY_TOTAL_SET',
            'GET_PROGRAM_PLAY_PLAY_SET',
            'GET_PROGRAM_PLAY_TOTAL_PART',
            'GET_PROGRAM_PLAY_CURRENT_PART',
        ]),
    },
    watch:{
        GET_PROGRAM_PLAY_CURRENT_PART(){
            this.Calculator()
        },
        GET_PROGRAM_PLAY_PLAY_SET(){
            this.Calculator()
        },
        GET_PROGRAM_PLAY_TOTAL_SET(){
            this.Calculator()
        }
    },
    mounted() {
        this.Calculator()
    },
    methods: {
        Calculator(){
            this.totalPart     = this.GET_PROGRAM_PLAY_TOTAL_PART

            const totalSet      = this.GET_PROGRAM_PLAY_TOTAL_SET
            const currentSet    = this.GET_PROGRAM_PLAY_PLAY_SET
            const totalPart     = this.GET_PROGRAM_PLAY_TOTAL_PART
            const currentPart   = this.GET_PROGRAM_PLAY_CURRENT_PART

            let partGroup = []
            for(let n = 0; totalSet > n; n++){
                for(let i = 0; totalPart > i; i++){
                    if(currentPart-1 > i && currentSet === n+1 || currentSet > n+1 ){
                        // 운동을 완료한 part
                        partGroup.push('white')
                    }else if(currentPart-1 === i && currentSet === n+1){
                        // 현재 진행중인 part
                        partGroup.push('#fff5a2')
                    }else{
                        // 운동을 하지 않은 part
                        partGroup.push('grey')
                    }
                }
            }
            // console.log('exercisePartItems', partGroup)
            this.exercisePartItems = partGroup
        },
        Stop(e){
            this.$emit('stop', e)
        }
    },
    
}
</script>

<style scoped lang="scss">
.time{
    font-size: var(--font-size);
}
.time_persent_area{
    width: 100%;
    margin-top: 1rem;
    margin-left: 0.5rem;

    .time_persent{
        width: 32%;
        height: 0.85rem;
        margin-right: 0.2rem;
    }
}
.play_button{
    width: 100% !important;
    height: 100% !important;
    font-size: 6.5rem;
}
</style>