<template>
    <v-row class="count_time_set" align="start">
        <v-col cols="4" class="pt-1" align="start">
            <v-icon dark class="count_time_set mr-1 mb-2">apps</v-icon>
            <span>{{GET_PART_PLAY_TOTAL_SET}}/{{GET_PART_PLAY_PLAY_SET+1}}셋트</span>
        </v-col>
        <v-col cols="4" class="pt-1 pl-4" align="start">
            <v-icon dark class="count_time_set mr-1 mb-2">timer</v-icon>
            <span>{{GET_PART_PLAY_EXERCISE_TIME}}</span>
        </v-col>
        <v-col cols="4" v-if="GET_PART_PLAY_WEIGHT > 0" class="pt-1" align="start">
            <v-icon dark class="count_time_set mr-1 mb-2">fitness_center</v-icon>
            <span>{{GET_PART_PLAY_WEIGHT}}kg</span>
        </v-col>
    </v-row>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
    name: 'CountTime',
    data:()=>({
    }),
    computed:{
        ...mapGetters([
            'GET_PART_PLAY_TOTAL_SET',
            'GET_PART_PLAY_PLAY_SET',
            'GET_PART_PLAY_WEIGHT',
            'GET_PART_PLAY_EXERCISE_TIME'
        ])
    },
    mounted() {
    },
    methods: {
        SelectSet (i) {
            let style = {}
            if(this.countItems.setCount === i){
                style = {
                    backgroundColor: '#fff5a2'
                }
            }
            return style
        },
    },
}
</script>

<style scoped lang="scss">
.count_time_btn{
    font-size: var(--font-size);
    font-style: italic;
    padding-right: 0.25rem;
    width: 3rem;
    height: 3rem;
}
.count_time_set{
    font-size: var(--font-size-medium);
}
</style>