<template>
    <v-dialog
        v-model="dialog"
        persistent
        width="550"
    >
        <v-card class="dialog_area">
            <v-card-title class="pt-3 pb-0">
                <v-col
                    align="start"
                    justify="center"
                    class="dialog_title"
                >
                    {{dialogItems.title}}
                </v-col>
            </v-card-title>

            <v-card-text class="pt-8 pa-5 pb-8">
                <v-col
                    align="center"
                    justify="center"
                    class="dialog_text"
                >
                    {{dialogItems.text}}
                </v-col>
            </v-card-text>

            <v-divider></v-divider>

            <v-card-actions class="pa-0">
                <v-col
                    align="center"
                    justify="center"
                    class="pa-0"
                >
                    <v-btn
                        width="50%"
                        color="secondary"
                        x-large
                        @click.stop="CancelPopup(this)"
                    >
                        <span v-if="dialogItems.close">{{dialogItems.close}}</span>
                        <span v-else>취소</span>
                    </v-btn>
                    <v-btn
                        width="50%"
                        class="dialog_button"
                        color="primary"
                        x-large
                        @click.stop="ConfirmPopup(this)"
                    >
                        <span v-if="dialogItems.confirm">{{dialogItems.confirm}}</span>
                        <span v-else>확인</span>
                    </v-btn>
                </v-col>
            </v-card-actions>
        </v-card>
    </v-dialog>
</template>

<script>
export default {
    name: 'ConfirmDialog',
    props:['dialog','dialogItems'],
    data: () => ({
    }),
    computed: {
    },
    created(){
    },
    mounted(){
    },
    methods:{
        CancelPopup(e) {
            this.$emit('close-cancel', e)
        },
        ConfirmPopup(e){
            this.$emit('close-allow', e)
        }
    }
}
</script>

<style>
.dialog_area{
    background: var(--primary-popup-bg) !important;
}
.dialog_title{
    color: rgb(192, 192, 192) !important;
}
.dialog_text{
    color: white !important;
    font-size: var(--font-size);
    line-height: var(--font-size-normal);
}
.dialog_button{
    font-weight: 500;
    color: black !important;
}
</style>
