import Vue from 'vue';
import Vuetify from 'vuetify/lib';

Vue.use(Vuetify);

export default new Vuetify({
    theme: {
        themes: {
            light: {
                primary:'#fff5a2',
                gray:'#dfdfdf'
            },
            dark: {
                primary:'#fff5a2',
                gray:'#dfdfdf'
            }
        },
    },
    iconfont: 'md' || 'mdi' || 'fa' || 'fa4',
    options: {
        customProperties: true
    }
});
