import moment from 'moment'
import { mapGetters } from 'vuex'

export default {
    data: () => ({
    }),
    watch: {
    },
    computed: {
        ...mapGetters([
            'GET_MENU_SELECT_MENU'
        ])
    },
    created(){
    },
    mounted(){
    },
    updated(){
    },
    destroyed(){
    },
    methods:{
        common_back_link(){
            let firstLink
            switch (this.GET_MENU_SELECT_MENU) {
                case '오늘의운동':
                    firstLink = '/user/exercise/today'
                    break;
                case '프리웨이트':
                    firstLink = '/user/exercise/freeWeight'
                    break;
                case '스트레칭':
                    firstLink = '/user/exercise/stretching'
                    break;
                case '서킷트레이닝':
                    firstLink = '/user/exercise/circuit'
                    break;
                case '요가':
                    firstLink = '/user/exercise/yoga'
                    break;
                default:
                    firstLink = '/user/main'
                    break;
            }
            return firstLink
        },
        common_audio_start(time){
            let audio
            if(time === 1){
                audio = new Audio(require('@/assets/audio/start.wav'))
                audio.play()
                return
            }if(time === 2 || time === 3){
                audio = new Audio(require('@/assets/audio/ready.wav'))
                audio.play()
                return
            }
        },
        common_check_motion(value) {
            let style = {}
            if(value === true){
                style = {
                    backgroundColor: '#fff5a2',
                    color:'black',
                    paddingTop: '0.85rem',
                    paddingBottom: '0.85rem'
                }
            }
            return style
        },
        // 선택 되어진 버튼 위치로 scroll (가로)
        common_select_scroll(select){
            if(this.$refs[select] === undefined) return
            const scroll = this.$refs[select][0]
            if(scroll !== null) scroll.scrollIntoView({inline: "center", behavior: "smooth"}) 

            // id로 하는 방법
            // const scroll = document.getElementById(select)
            // if(scroll === undefined) return
            // scroll.scrollIntoView({inline: "center", behavior: "smooth"}) 
        },
        // start time
        common_set_start_time(){
            const time = moment().format('YYYYMMDDHHmmss')
            return time
        },
        // time
        common_time_string(time_secode){            
            const minute = parseInt(time_secode / 60)
            const secode = time_secode % 60
            
            let minute_string = (minute < 10)? '0'+minute : minute
            let second_string = (secode < 10)? '0'+secode : secode
            let time_string = minute_string + ':' + second_string

            return time_string
        },
    }
}
