<template>
    <v-col class="fill-height">
        <layout-header-bi-x/>
        <v-row align="center" class="ml-10 mr-10 mt-3">
            <v-col cols="4" align-self="start" v-if="playItems">
                <play-test-items-count-time
                    :title="playItems.title"
                    :countItems="playItems.countItems"
                />
            </v-col>
            <v-col cols="8" align="end" v-if="playItems">
                <!-- <video></video> -->
                <img :src="playItems.exerciseItems.image" height="150">
            </v-col>
        </v-row>
        <play-text-page
            v-if="textPageOn"
            :textPageItem="textPageItem"
            v-on:close-text="CloseText()"
        />
        <play-test-items-times-of-exercise-self
            v-if="timesOfExerciseItemsOn"
            :timesOfExerciseItems="playItems"
        />
    </v-col>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'
import timer_part from '@/mixins/timer_part.js'
import common from '@/mixins/common.js'
import exercise from '@/mixins/exercise.js'

export default {
    name: 'UncompletedExerciseSelf',
    props:['playItems'],
    mixins:[ timer_part, exercise, common ],
    data:()=>({
        textPageOn: false ,
        textPageItem: '',
        exerciseImage: null,
        timesOfExerciseItemsOn: false,
        nextExerciseItemsOn: false,
        nextExerciseItems:{},
    }),
    computed:{
        ...mapGetters([
            'GET_PART_PLAY_PLAY_SET',
        ]),
    },
    watch:{
        textPageOn(value){
            if (value === false){
                this.MU_PART_PLAY_EXERCISE(true)
            }
        },
        GET_PART_PLAY_PLAY_SET(){
            this.textPageOn = true
            this.EditPlayItems()
        }
    },
    mounted() {
        this.textPageOn = true
        this.timer_part_start()
        this.EditPlayItems()
        this.SetStartTime()
    },
    destroyed() {
        this.timer_part_stop()
    },
    methods: {
        ...mapMutations([
            'MU_PART_PLAY_EXERCISE',
            'MU_PART_PLAY_PLAY_ITEMS_SET_START_TIME',
        ]),
        CloseText(){
            this.textPageOn = false
            this.timesOfExerciseItemsOn = true
        },
        EditPlayItems(){
            // title
            this.textPageItem = `${this.GET_PART_PLAY_PLAY_SET + 1}세트를 시작합니다.`
            // nextExercise
            this.nextExerciseItems = {
                exerciseTime: 16,
                time: 7,
                title: this.playItems.title
            }
        },
        SetStartTime(){
            // setStartTime
            const startTime = this.common_set_start_time()
            this.MU_PART_PLAY_PLAY_ITEMS_SET_START_TIME(startTime)
        }
    },
}
</script>

