<template>
    <v-row class="times_of_exercise">
        <v-col align-self="center" align="center">
            <v-row align="center" justify="center">
                <span class="times_of_exercise_text">목표</span>
                <span class="times_of_exercise_count">{{timesOfExerciseItems.countItems.goalCount}}</span>
                <span class="times_of_exercise_text">회</span>
            </v-row>
            <v-col>
                <v-col class="times_of_exercise_description">
                    {{timesOfExerciseItems.title}}을 {{timesOfExerciseItems.countItems.goalCount}}회 진행후 <br>
                    운동완료 버튼을 누르세요
                </v-col>
                <v-btn 
                    color="#fff5a2"
                    width="300"
                    height="120" 
                    class="times_of_exercise_button"
                    @click="ExerciseSuccess()"
                >
                    운동 완료
                </v-btn>
            </v-col>
        </v-col>
        <play-next-exercise-time
            :dialog="nextExerciseItemsOn"
            :nextExerciseItems="nextExerciseItems"
            v-on:close-dialog="CloseDialog(this)"
        />
    </v-row>
</template>

<script>
import { mapMutations } from 'vuex'

export default {
    name: 'TimesOfExercise',
    props:['timesOfExerciseItems'],
    data:()=>({
        nextExerciseItemsOn: false,
        nextExerciseItems:{}
    }),
    computed:{
    },
    watch:{
    },
    mounted() {
        this.EditItems()
    },
    methods: {
        ...mapMutations([
            'MU_PART_PLAY_EXERCISE',
        ]),
        CloseDialog(){
            this.nextExerciseItemsOn = false
        },
        EditItems(){
            this.MU_PART_PLAY_EXERCISE(true)
            this.nextExerciseItems={
                exerciseTime: 16,
                time: 7,
                title: this.timesOfExerciseItems.title
            }
        },
        ExerciseSuccess(){
            this.MU_PART_PLAY_EXERCISE(false)
            this.nextExerciseItemsOn= true
        }
    },
}
</script>

<style scoped lang="scss">
    .times_of_exercise{
        height: 65%;

    .times_of_exercise_count{
        font-size: 7rem;
        font-style: italic;
        margin-right: 1rem;
    }
    .times_of_exercise_text{
        font-size: var(--font-size-medium);
        padding-top: 3rem;
    }
    .times_of_exercise_button{
        margin-top: 1rem;
        font-size: var(--font-size-medium);
        font-weight: 500;
    }
    .times_of_exercise_description{
        color: #fff5a2;
        font-size: var(--font-size-small);
        padding-top: 1rem;
    }
}
</style>