<template>
    <v-row class="pl-10 pr-10 pt-10">
        <v-btn text dark height="45" class="pa-0" @click="goBack">
            <img src="@/assets/images/common/icon_back.png">
        </v-btn>
        <v-spacer/>
        <img src="@/assets/images/common/icon_bi.png" class="button-right">
    </v-row>
</template>

<script>
import common from '@/mixins/common.js'
export default {
    name: 'Header',
    mixins:[ common ],
    data:()=>({
    }),
    computed: {
    },
    methods: {
        goBack() {
            if(process.env.VUE_APP_MODE === '2'){
                this.$router.push('/user/mainGX')
            }else{
                // 되돌아갈 Link
                if( this.$route.name !== 'Program'){
                    // route name이 Program일 경우 메인 화면으로 
                    this.$router.push('/user/main')
                }else{
                    const firstLink = this.common_back_link()
                    this.$router.push(firstLink)
                }
            }
        }
    }
}
</script>
