<template>
    <v-col cols="12" align="start">
        <v-row justify="start">
            <template v-for="i in countItems.set">
                <div :key="i">
                    <v-btn 
                        v-if="countItems.setCount >= i"
                        fab
                        class="count_btn" 
                        :style="SelectSet(i)"
                        :color="selectColor"
                    >
                        {{i}}
                    </v-btn>
                    <v-btn 
                        v-else
                        fab
                        class="count_btn"
                        outlined
                    >
                        {{i}}
                    </v-btn>
                    <v-icon dark v-if="i < countItems.set">
                        horizontal_rule
                    </v-icon>
                </div>
            </template>
        </v-row>
        <v-row class="count_set">
            <v-col v-if="title" cols="12" class="mt-2">
                <span class="count_title">{{title}}</span>
            </v-col>
            <v-col cols="5" class="pt-1" align="start">
                <v-icon dark class="count_set mr-1 mb-2">apps</v-icon>
                <span>{{countItems.setCount}}/{{countItems.set}}셋트</span>
            </v-col>
            <v-col class="pt-1" align="start">
                <v-icon dark class="count_set mr-1 mb-2">timelapse</v-icon>
                <span>{{countItems.count}}회</span>
            </v-col>
            <v-col v-if="countItems.weight" class="pt-1" align="start">
                <v-icon dark class="count_set mr-1 mb-2">fitness_center</v-icon>
                <span>{{countItems.weight}}kg</span>
            </v-col>
        </v-row>
    </v-col>
</template>

<script>
export default {
    name: 'Count',
    props:['title','countItems'],
    data:()=>({
        selectColor:'',
    }),
    computed:{
    },
    methods: {
        SelectSet (i) {
            let style = {}
            if(this.countItems.setCount === i){
                style = {
                    backgroundColor: '#fff5a2',
                    color: 'black'
                }
            }else if(this.countItems.setCount > i){
                style = {
                    backgroundColor: 'white',
                    color: 'black'
                }
            }else{
                this.selectColor = 'black'
            }
            return style
        },
    },
    mounted() {
    }
}
</script>

<style scoped lang="scss">
.count_btn{
    font-size: var(--font-size);
    font-style: italic;
    color: white;
    padding-right: 0.25rem;
    width: 3rem;
    height: 3rem;
}
.count_title{
    font-size: var(--font-size-normal);
}
.count_set{
    font-size: var(--font-size-medium);
}
</style>