import api from '../../utils/apiCaller'
import router from '../../router'
import localStorage from '../../utils/localStorage'

const state = {
    authMemberInfo  : null,     // 회원 승인 정보
    memberInfo      : null,     // 회원 정보
}

const mutations = {
    MU_APP_AUTH_MEMBER_KEY (state, authMemberInfo) {
        state.authMemberInfo = authMemberInfo
    },
    MU_APP_MEMBER_INFO (state, memberInfo) {
        state.memberInfo = memberInfo
    },
    MU_APP_LOGOUT (state, data) {
        state.authMemberInfo    = data
        state.memberInfo        = data
    },
}

const actions = {
    async ACT_APP_API_AUTH_MEMBER_BY_NUMBER ({ commit }, payload) {
        let authMemberInfo = await api.app.API_AUTH_MEMBER_BY_NUMBER(payload)
        
        if(authMemberInfo === null) return
        commit('MU_APP_AUTH_MEMBER_KEY', authMemberInfo)
        // localstorage save
        let memAuthKey = authMemberInfo.memAuthKey
        localStorage.setItem('memAuthKey', memAuthKey)
    },
    async ACT_APP_API_AUTH_MEMBER_BY_CARD ({ commit }, payload) {
        let authMemberInfo = await api.app.API_AUTH_MEMBER_BY_CARD(payload)
        
        if(authMemberInfo === null) return
        commit('MU_APP_AUTH_MEMBER_KEY', authMemberInfo)
        // localstorage save
        let memAuthKey = authMemberInfo.memAuthKey
        localStorage.setItem('memAuthKey', memAuthKey)
    },
    async ACT_APP_API_GET_MEMBER_INFO ({commit}) {
        if(localStorage.getItem('memAuthKey')) {
            let memAuthKey = { memAuthKey: localStorage.getItem('memAuthKey') }
            let memberInfo = await api.app.API_GET_MEMBER_INFO(memAuthKey)
            commit('MU_APP_MEMBER_INFO', memberInfo)
        }else{
            router.push('/')
        }
    },
    ACT_APP_LOGOUT ({commit}) {
        localStorage.setItem('memAuthKey', null)
        commit('MU_APP_LOGOUT',null)
        router.push('/')
    },
}

const getters = {
    GET_APP_AUTH_MEMBER_INFO (state) {
        return state.authMemberInfo
    },
    GET_APP_MEMBER_INFO (state) {
        return state.memberInfo
    }
}

export default {
    state,
    mutations,
    actions,
    getters
}
