const setItem = (key, value) => {
    localStorage.setItem(key, value)
}

const getItem = (key) => {
    const value = localStorage.getItem(key)
    try {
        return JSON.parse(value)
    } catch (e) {
        console.log('error',e)
    }

    if (Number(value).toString() !== 'NaN') {
        return Number(value)
    }

    if(value === 'true' || value === 'false') {
        return Boolean(value)
    }

    return value
}

export default {
    setItem,
    getItem
}

