import axios from 'axios'

const apiUrl = process.env.VUE_APP_API_SERVER

const caller = {
    async call (method, params = { }) {
        const code = `${Math.random().toString(36).substr(2).toUpperCase()}`

        const req = {
            jsonrpc: '2.0',
            method: method,
            params: params,
            id: code
        }

        // axios 호출시 빈값 받지 않게 option 추가
        const config = {
            headers: {
                'accept': 'application/json',
                'content-type': 'application/x-www-form-urlencoded; charset=UTF-8'
            }
        }

        const res = await axios.post(apiUrl, req, config)
        .then((resp) => {
            // console.log('[API]',resp)
            if(resp.data.error.code > 0) {
                const code      = resp.data.error.code
                const message   = resp.data.error.message
                this.$toastr.error( code, message, {clickClose: true})
                // alert(message)
            }
            return resp.data.result
        })
        .catch(e => {
            console.log('error',e)
        })
        
        return res
    }
}

