<template>
    <v-dialog
        v-model="dialog"
        persistent
    >
        <v-row class="next_exercise_time_area" align="center" justify="center">
            <v-col align="center">
                <v-col 
                    class="next_exercise_time_description"
                >
                    <p><span>{{GET_MENU_SELECT_MENU}}</span>을 수행할 셋트를 선택해 주세요.</p>
                </v-col>
                <play-program-items-select-set
                    :selectItems="selectItems"
                    v-on:close-dialog="CloseDialog(this)"
                />
            </v-col>
        </v-row>
    </v-dialog>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
    name: 'NextExercise',
    props:['dialog'],
    data:()=>({
        selectItems:{}
    }),
    computed:{
        ...mapGetters([
            'GET_MENU_SELECT_MENU'
        ])
    },
    watch:{
    },
    mounted() {
        this.EditNextExerciseItems()
    },
    methods: {
        CloseDialog(e){
            this.$emit('close-dialog', e)
        },
        EditNextExerciseItems(){
            this.selectItems={
                title: this.GET_MENU_SELECT_MENU,
                exerciseCount: 5
            }
        }
    },
}
</script>

<style scoped lang="scss">
.next_exercise_time_area{
    padding-top: 2rem !important;
    padding-bottom: 5rem !important;
    background-color: var(--primary-popup-bg);

    .next_exercise_time_description{
        color: white;
        font-size: var(--font-size-normal);
        margin: 6rem 0;

        span{
            font-size: var(--font-size-normal) !important;
            color: var(--primary-main);
        }
    }
    .next_exercise_time_description span{
        font-size: var(--font-size-large);
    }
}
</style>