<template>
    <v-dialog
        v-model="dialog"
        persistent
        width="800"
    >
        <v-card class="dialog_bg" v-if="explainItems.length > 0">
            <v-row align="center" class="ml-10 mr-10 mb-5 pt-10 pb-3">
                <span class="dialog_title">{{explainItems[explainIndex].name}}</span>
                <v-spacer/>
                <v-btn
                    dark
                    icon 
                    large 
                    @click.stop="closePopup(this)"
                >
                    <v-icon size="40">close</v-icon>
                </v-btn>
            </v-row>
            <v-col align="center">
                <v-row align="center" justify="center">
                    <v-btn icon x-large dark @click="listBtn('back')">
                        <v-icon class="dialog_btn_icon">chevron_left</v-icon>
                    </v-btn>
                    <img :src="explainItems[explainIndex].image">
                    <v-btn icon x-large dark @click="listBtn('next')">
                        <v-icon class="dialog_btn_icon">chevron_right</v-icon>
                    </v-btn>
                </v-row>
                <v-col class="dialog_description pa-0 pb-10">
                    {{explainItems[explainIndex].howTo}}
                </v-col>
            </v-col>
        </v-card>
    </v-dialog>
</template>

<script>
export default {
    name: 'ExerciseExplain',
    props: ['dialog','explainItems','index'],
    data:()=>({
        explainIndex: 0
    }),
    computed: {
    },
    watch:{
        index(value){
            this.explainIndex = value
        }
    },
    mounted(){
    },
    methods:{
        closePopup(e) {
            this.$emit('close-dialog', e);
        },
        listBtn(method){
            if(method === 'back'){
                if(this.explainIndex > 0){
                    this.explainIndex -= 1
                }
            }else if(method === 'next'){
                if(this.explainItems.length > this.explainIndex+1){
                    this.explainIndex += 1
                }
            }
        }
    }
}
</script>

<style scoped lang="scss">
.dialog_bg{
    background: var(--primary-main-bg);
}
.dialog_title{
    font-size: var(--font-size-medium);
    color: white;
}
.dialog_btn{
    font-size: var(--font-size-medium);
    color: white;
}
.dialog_btn_icon{
    font-size: var(--font-size-medium);
    color: white;
}
.dialog_description{
    text-align: left;
    margin: 3rem;
    width: auto;
    font-size: var(--font-size-small);
    line-height: 2rem;
    color: white;
}
</style>
