<template>
    <v-col align="center" class="fill-height">
        <v-row align="center" class="text_wrapper ml-10">
            <v-col align="center">
                <p class="text_title">{{textPageItem}}</p>
            </v-col>
        </v-row>
    </v-col>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'

export default {
    name: 'TextPage',
    props:['textPageItem'],
    data:()=>({
    }),
    computed:{
        ...mapGetters([
            'GET_PART_PLAY_EXERCISE_DELAY'
        ])
    },
    watch:{
        GET_PART_PLAY_EXERCISE_DELAY(value){
            if(!value){
                this.closePopup(this)
            }
        }
    },
    mounted() {
        this.MU_PART_PLAY_EXERCISE_DELAY(true)
    },
    methods: {
        ...mapMutations([
            'MU_PART_PLAY_EXERCISE_DELAY'
        ]),
        closePopup(e){
            this.$emit('close-text', e);
        }
    },
}
</script>

<style scoped lang="scss">
.text_wrapper{
    height: 70%;
}
.text_description{
    font-size: var(--font-size);
}
.text_title{
    font-size: var(--font-size-large);
}
</style>