import { mapGetters, mapMutations } from 'vuex'

export default {
    data: () => ({
        timer_program: 0,
        timer_program_exercise:'',
        timer_program_exercise_minute: 0,
        timer_program_exercise_secode: 0,
        timer_program_rest_secode: 0,
        timer_program_delay: 1,
        timer_program_pause_minute: 0,
        timer_program_pause_secode: 0,
        timer_program_exercise_time: 0,
    }),
    computed: {
        ...mapGetters([
            'GET_MENU_SELECT_EXERCISE',
            'GET_MENU_SELECT_PROGRAM',
            'GET_PROGRAM_PLAY_EXERCISE_SET_START',
            'GET_PROGRAM_PLAY_EXERCISE',
            'GET_PROGRAM_PLAY_EXERCISE_MODE',
            'GET_PROGRAM_PLAY_TOTAL_PART',
            'GET_PROGRAM_PLAY_CURRENT_PART',
            'GET_PROGRAM_PLAY_EXERCISE_DELAY',
            'GET_PROGRAM_PLAY_PAUSE',
            'GET_PROGRAM_PLAY_TOTAL_EXERCISE_TIME',
            'GET_PROGRAM_PLAY_TOTAL_REST_TIME',
            'GET_PROGRAM_PLAY_REST_SET',
            'GET_PROGRAM_PLAY_PLAY_SET',
        ]),
    },
    watch: {
        GET_PROGRAM_PLAY_EXERCISE(value){
            if(value){
                this.timer_program_rest_set()
            }
        },
        GET_PROGRAM_PLAY_PAUSE(value){
            if(!value) {
                this.timer_program_pause_minute = 0
                this.timer_program_pause_secode = 0
            }
        },
        timer_program_exercise_secode(value){
            if(value > -1){
                if(value === 60){
                    this.timer_program_exercise_secode = 0
                    this.timer_program_exercise_minute += 1
                }
                let minute = (this.timer_program_exercise_minute < 10)? '0'+this.timer_program_exercise_minute : this.timer_program_exercise_minute
                let second = (this.timer_program_exercise_secode < 10)? '0'+this.timer_program_exercise_secode : this.timer_program_exercise_secode
                this.timer_program_exercise = minute + ':' + second
            }
        },
        timer_program_exercise_time(value){
            if(value === 0) {
                // part 운동 종료
                this.MU_PROGRAM_PLAY_EXERCISE(false)

                // PART 운동이 끝나면 SET정보 +1 PART정보 RESET
                if( this.GET_PROGRAM_PLAY_CURRENT_PART === this.GET_PROGRAM_PLAY_TOTAL_PART){
                    let set = this.GET_PROGRAM_PLAY_PLAY_SET + 1
                    this.MU_PROGRAM_PLAY_PLAY_SET(set)
                    this.MU_PROGRAM_PLAY_CURRENT_PART(0)
                }
            }
        },
        timer_program_rest_secode(value){
            this.MU_PROGRAM_PLAY_REST_TIME(value)
            if(value === 0){

                if(this.GET_PROGRAM_PLAY_PLAY_SET === 0 ){
                    let set = this.GET_PROGRAM_PLAY_PLAY_SET + 1
                    this.MU_PROGRAM_PLAY_PLAY_SET(set)
                }
                // 셋트 시작인 경우 rest 화면 먼저 출력
                if(this.GET_PROGRAM_PLAY_PLAY_SET !== 1 && this.GET_PROGRAM_PLAY_CURRENT_PART === 0 && !this.GET_PROGRAM_PLAY_EXERCISE_SET_START){
                    this.MU_PROGRAM_PLAY_EXERCISE_SET_START(true)
                    this.timer_program_rest_set()
                    this.MU_PROGRAM_PLAY_EXERCISE(false)
                    return
                }else{
                    this.MU_PROGRAM_PLAY_EXERCISE_SET_START(false)
                    this.MU_PROGRAM_PLAY_EXERCISE(true)
                }

                let part = this.GET_PROGRAM_PLAY_CURRENT_PART + 1
                this.MU_PROGRAM_PLAY_CURRENT_PART(part)
                
                this.timer_program_setting()
            }
        },
        timer_program_exercise(value){
            this.MU_PROGRAM_PLAY_EXERCISE_TIME(value)
        },
        timer_program(){
            if(this.GET_PROGRAM_PLAY_EXERCISE_DELAY){
                this.timer_program_delay -= 1
                if(this.timer_program_delay === 0){
                    this.timer_program_delay = 1
                    this.MU_PROGRAM_PLAY_EXERCISE_DELAY(false)
                }
            }else{
                if(this.GET_PROGRAM_PLAY_EXERCISE){
                    // total time
                    this.timer_program_exercise_secode += 1
                    let totalExerciseTime = this.GET_PROGRAM_PLAY_TOTAL_EXERCISE_TIME + 1
                    this.MU_PROGRAM_PLAY_TOTAL_EXERCISE_TIME(totalExerciseTime)
                    
                    if(this.GET_PROGRAM_PLAY_EXERCISE_MODE === 1){
                        // exercise time
                        this.timer_program_exercise_time -= 1
                        this.MU_PROGRAM_PLAY_START_TIME(this.timer_program_exercise_time)
                    }
                }else{
                    this.timer_program_rest_secode -= 1
                    let totalRestTime = this.GET_PROGRAM_PLAY_TOTAL_REST_TIME + 1
                    this.MU_PROGRAM_PLAY_TOTAL_REST_TIME(totalRestTime)
                }
            }
        },
        timer_program_pause_secode(value){
            if(this.GET_PROGRAM_PLAY_PAUSE){
                if(value === 60){
                    this.timer_program_pause_secode = 0
                    this.timer_program_pause_minute += 1
                }
                let minute = (this.timer_program_pause_minute < 10)? '0'+this.timer_program_pause_minute : this.timer_program_pause_minute
                let second = (this.timer_program_pause_secode < 10)? '0'+this.timer_program_pause_secode : this.timer_program_pause_secode
                let timer_program_pause = minute + ':' + second

                this.MU_PROGRAM_PLAY_PAUSE_TIME(timer_program_pause)
            }
        }
    },
    created(){
        // 등록된 Rest time 설정
        this.timer_program_rest_set()
    },
    mounted(){
    },
    updated(){
    },
    destroyed(){
    },
    methods:{
        ...mapMutations([
            // PROGRAM TIME
            'MU_PROGRAM_PLAY_EXERCISE_DELAY',
            'MU_PROGRAM_PLAY_EXERCISE_SET_START',
            'MU_PROGRAM_PLAY_EXERCISE',
            'MU_PROGRAM_PLAY_EXERCISE_TIME',
            'MU_PROGRAM_PLAY_REST_TIME',
            'MU_PROGRAM_PLAY_TOTAL_EXERCISE_TIME',
            'MU_PROGRAM_PLAY_TOTAL_REST_TIME',
            'MU_PROGRAM_PLAY_PAUSE_TIME',
            'MU_PROGRAM_PLAY_START_SET',
            'MU_PROGRAM_PLAY_PLAY_SET',
            'MU_PROGRAM_PLAY_START_TIME',
            'MU_PROGRAM_PLAY_CURRENT_PART',
        ]),
        timer_program_setting(){
            let exTime = this.GET_MENU_SELECT_PROGRAM[this.GET_PROGRAM_PLAY_CURRENT_PART-1].exTime
            if(exTime){
                this.timer_program_exercise_time = exTime
                this.MU_PROGRAM_PLAY_START_SET(exTime)
                this.MU_PROGRAM_PLAY_START_TIME(exTime)
            }
        },
        timer_program_rest_set(){
            this.timer_program_rest_secode = this.GET_PROGRAM_PLAY_REST_SET
        },
        timer_program_start() {
            this.interval_program_timer = setInterval(() => {
                if(this.GET_PROGRAM_PLAY_PAUSE){
                    this.timer_program_pause_secode += 1
                }else{
                    this.timer_program += 1
                }
            },1000)
        },
        timer_program_stop(){
            clearInterval(this.interval_program_timer)
        },
    }
}
