<template>
    <v-dialog
        v-model="dialog"
        persistent
        width="80%"
        height="100%"
    > 
        <v-card class="input_area">
            <v-card-text>
                <v-row class="pl-10 pr-10 pt-10">
                    <img src="@/assets/images/common/icon_bi.png" width="169" height="42">
                    <v-spacer/>
                    <v-btn text dark class="pa-0" height="100%" @click="CancelPopup()">
                        <img src="@/assets/images/icons/icon_close.png">
                    </v-btn>
                </v-row>
                <v-row
                    align="center"
                    justify="center"
                    class="input_title"
                >
                    휴대폰 번호를 입력해주세요.
                </v-row>
                <v-row
                    align="center"
                    justify="center"
                    class="input_number"
                >
                    <template v-for="(select, i) in selectItems">
                        <span :key="i">
                            <span v-if="i < 7" class="input_number_text">{{select}}</span>
                            <span v-else class="input_number_password">*</span>
                        </span>
                    </template>
                </v-row>
            </v-card-text>
            <v-card-text class="input_button_group">
                <v-col
                    align="center"
                    justify="center"
                    class="input_grid_container"
                >
                    <template v-for="(number, i) in numberItems">
                        <v-btn
                            :key="i"
                            class="input_number_button"
                            :color="InputNumberColor(number)"
                            @click.stop="SelectButton(number)"
                        >
                            <span>{{number}}</span>
                        </v-btn>
                    </template>
                </v-col>
            </v-card-text>
        </v-card>
    </v-dialog>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'

export default {
    name: 'InputNumber',
    props:['dialog'],
    data: () => ({
        numberItems:[7,8,9,4,5,6,1,2,3,'←',0,'확인'],
        selectItems:[0,1,0]
    }),
    watch:{
    },
    computed: {
        ...mapGetters([
            'GET_APP_AUTH_MEMBER_INFO'
        ])
    },
    created(){
    },
    mounted(){
    },
    destroyed(){
        this.ResetItems()
    },
    methods:{
        ...mapActions([
            'ACT_APP_API_AUTH_MEMBER_BY_NUMBER',
            'ACT_APP_API_GET_MEMBER_INFO',
        ]),
        InputNumberColor(number){
            let color = 'gray'
            if(number === '←'){
                color = 'error'
            }if(number === '확인'){
                color = 'primary'
            }
            return color
        },
        DeleteNumber(){
            if(this.selectItems.length > 3){
                this.selectItems.pop()
            }
        },
        SelectButton(select){
            if(this.selectItems.length < 11 && typeof(select) === 'number'){
                this.selectItems.push(select)
            }else{
                if(select === '확인'){
                    this.ConfirmNumber()
                }if(select === '←'){
                    this.DeleteNumber()
                }
            }
        },
        async ConfirmNumber(){
            if(this.selectItems.length > 10){
                const number = this.selectItems.join('')

                await this.ACT_APP_API_AUTH_MEMBER_BY_NUMBER(number)
                if(this.GET_APP_AUTH_MEMBER_INFO === null) {
                    this.$toastr.error  ( 
                        '로그인 오류', 
                        '로그인 멤버 정보가 없습니다.' 
                        ,{
                            position: 'toast-top-center',
                            clickClose: true
                        }
                    )
                    return
                }            
                await this.ACT_APP_API_GET_MEMBER_INFO()             // get member info
                .then(()=>{
                    this.$router.push('/user/welcome')
                })
            }else{
                this.$toastr.error( '형식에 맞지 않는 번호입니다. 다시 확인하고 입력해주세요.', '입력값 오류', {clickClose: true})
            }
        },
        CancelPopup(e) {
            this.ResetItems()
            this.$emit('close-dialog', e)
        },
        ResetItems(){
            this.selectItems = [0,1,0]
        }
    }
}
</script>

<style>
.input_area{
    height: 90vh;
    background: var(--primary-popup-bg) !important;
}
.input_grid_container {
    width: 55%;
    position: absolute;

    display: grid;
    transform: translate(39%, 20%);

    gap: 20px 50px;
    grid-template-columns: repeat(3, 1fr);
    grid-auto-rows: minmax(auto, 80px);
}
.input_title{
    padding-top: 2rem;
    color: rgb(192, 192, 192) !important;
    font-size: var(--font-size-medium);
}
.input_number{
    color: white !important;
    font-size: var(--font-size-large);
    vertical-align: middle;
    padding-top: 6rem;
}
.input_number_button{
    font-weight: 700;
    color: black !important;
    padding: 3.6rem 0 !important;
    font-size: var(--font-size-medium) !important;
}
.input_button{
    font-weight: 500;
    color: black !important;
}
.input_number_text{
    vertical-align: 12px;
    margin:0 0.1rem;
}
.input_number_password{
    vertical-align: middle;
    margin:0 0.4rem;
}
</style>
