import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
    {
        path: '/',
        component: () => import('@/views/idle'),
    },
    {
        path: '/user',
        name: 'User',
        component: () => import('@/views/user'),
        children: [
            {
                path: 'welcome',
                name: 'Welcome',
                component: () => import('@/views/welcome'),
            },
            {
                path: 'main',
                name: 'Main',
                component: () => import('@/views/main'),
            },
            {
                path: 'mainGX',
                name: 'MainGX',
                component: () => import('@/views/mainGX'),
            },
            {
                path: 'exercise/today',
                name: 'Today',
                component: () => import('@/views/exercise/today'),
            },
            {
                path: 'exercise/stretching',
                name: 'Stretching',
                component: () => import('@/views/exercise/stretching'),
            },
            {
                path: 'exercise/freeWeight',
                name: 'FreeWeight',
                component: () => import('@/views/exercise/freeWeight'),
            },
            {
                path: 'exercise/circuit',
                name: 'Circuit',
                component: () => import('@/views/exercise/circuit'),
            },
            {
                path: 'exercise/yoga',
                name: 'Yoga',
                component: () => import('@/views/exercise/yoga'),
            },
            {
                path: 'exercise/program',
                name: 'Program',
                component: () => import('@/views/exercise/program'),
            },
            {
                path: 'exercise/partPlay',
                name: 'PartPlay',
                component: () => import('@/views/play/partPlay'),
            },
            {
                path: 'exercise/programPlay',
                name: 'ProgramPlay',
                component: () => import('@/views/play/programPlay'),
            },
            {
                path: 'exercise/report',
                name: 'Report',
                component: () => import('@/views/play/report'),
            },
        ]   
    },
]

const router = new VueRouter({
    mode: 'hash',
    routes
})

export default router
