<template>
    <v-col align="center" justify="center">
        <v-col align="start">
            <div class="list_title">
                {{ title }}
            </div>
            <div v-if="description" class="list_description">
                {{ description }}
            </div>
        </v-col>
        <div class="thumbnail_wrapper mt-2">
            <template v-for="(program, i) in programList">
                <div 
                    class="thumbnail_card" 
                    :key="i" 
                    @click="onClickCard(i)"
                >
                    <v-img :src="program.image" class="thumbnail">
                        <v-row class="fill-height thumbnail_motion" justify="end" v-if="program.motion">
                            <v-btn fab small color="primary" class="mr-5 mt-5">
                                <v-icon color="black">accessibility_new</v-icon>
                            </v-btn>
                        </v-row>
                    </v-img>
                    <div class="thumbnail_title mt-1">
                        {{ i+1 }}. {{ program.name }}
                    </div>
                </div>
            </template>
        </div>
        <common-exercise-explain
            :dialog="dialog"
            :index="index"
            :explainItems="programList"
            v-on:close-dialog="CloseDialog()"
        />
    </v-col>
</template>

<script>
import { mapActions, mapMutations } from 'vuex'
export default {
    name: 'ProgramList',
    props: [
        'title',
        'description',
        'programList'
    ],
    data:()=>({
        dialog: false,
        index: 0
    }),
    computed: {
    },
    watch:{
        programList(){
            this.EditProgramList()
        }
    },
    mounted(){
    },
    methods: {
        ...mapActions([
            'ACT_MENU_API_GET_CIRCUIT_PROGRAM_HOW_TO',
            'ACT_MENU_API_GET_CIRCUIT_GUIDE',
        ]),
        ...mapMutations([
            'MU_MENU_SELECT_PROGRAM'
        ]),
        async EditProgramList(){
            for(const item of this.programList){
                // 설명 추가
                let HowTo = await this.ACT_MENU_API_GET_CIRCUIT_PROGRAM_HOW_TO(item.subExSortCode)
                
                // // Guide 추가
                // let Guide = await this.ACT_MENU_API_GET_CIRCUIT_GUIDE(item.subExSortCode)
                // console.log('Guide',Guide)

                item.howTo = Object(HowTo).exHowTo
            }
            this.MU_MENU_SELECT_PROGRAM(this.programList)
            // console.log('this.programList',this.programList)
        },
        onClickCard (index) {
            this.index = index
            this.dialog = true
        },
        CloseDialog(){
            this.dialog = false
        }
    }
}
</script>
<style scoped>
.list_title {
    font-size: 72px;
    font-weight: bold;
    padding-bottom: 0px;
    margin-bottom: 10px;
    display: flex;
}
.list_description {
    height: 54px;
    font-size: 36px;
    margin-bottom: 25px;
}

.thumbnail {
    width: 320px;
    height: 180px;
}
.thumbnail_wrapper {
    float: left;
    display: flex;
    flex-direction: row;
    -webkit-overflow-scrolling: touch;
    width: 100%;
    padding-right: 1rem;
    height: 230px;
    white-space: nowrap;
    overflow-x: auto;
}
.thumbnail_card {
    margin-right: 20px;
    margin-bottom: 20px;
}
.thumbnail_title {
    width: 320px;
    font-size: var(--font-size-small);
    text-align: center;
}

</style>