import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import vuetify from './plugins/vuetify'
import './components'

// toastr
import VueToastr2 from 'vue-toastr-2'
import 'vue-toastr-2/dist/vue-toastr-2.min.css'
window.toastr = require('toastr')
Vue.use(VueToastr2)

Vue.prototype.$eventBus = new Vue()

// 일렉트론 및 카드 사용env값에서만 동작하게 예외처리
if(process.env.VUE_APP_USE_CARD === 'true' && window.location.fragmentDirective !== undefined){  
  const CardReader = require('@/utils/cardReader').default
  //  카드리더
  new CardReader((uid) => {
    // console.log('CardReader loaded', uid)
    // 자동 로그 아웃
    // clearTimeout(window.timeOut)
    // window.timeOut = setTimeout(() => { router.push('/') }, 15000)
    Vue.prototype.$eventBus.$emit('E_CardReader', uid)
  })
}

Vue.config.productionTip = false

new Vue({
  router,
  store,
  vuetify,
  render: h => h(App)
}).$mount('#app')
