import api from '../../utils/apiCaller'
import localStorage from '../../utils/localStorage'

// import router from '../../router'

const state = {
    todayList           : null, // 오늘의 운동
    stretchList         : null, // 스트레칭
    stretchExProgramList: null, // 스트레칭 프로그램
    stretchExSortList   : null, // 스트레칭 부위별
    freeWeightList      : null, // 프리웨이트리스트
    circuitList         : null, // 서킷리스트
    yogaList            : null, // 요가리스트
    selectProgram       : null, // 선택한 프로그램
    selectExercise      : null, // 선택한 운동
    selectMenu          : null, // 선택한 menu
    exResultList        : null, // 운동결과 List
}

const mutations = {
    // Today
    MU_MENU_TODAY_LIST (state, todayList) {
        state.todayList = todayList.exSeqList
    },
    // Stretch
    MU_MENU_STRETCH_LIST (state, stretchList) {
        state.stretchList = stretchList
    },
    MU_MENU_STRETCH_EX_PROGRAM_LIST (state, stretchExProgramList) {
        state.stretchExProgramList = stretchExProgramList.programList
    },
    MU_MENU_STRETCH_EX_SORT_LIST (state, stretchExSortList) {
        state.stretchExSortList = stretchExSortList.exSortList
    },
    // Free Weight
    MU_MENU_PART_LIST (state, freeWeightList) {
        state.freeWeightList = freeWeightList.exList
    },
    // Circuit
    MU_MENU_CIRCUIT_LIST (state, circuitList) {
        state.circuitList = circuitList.exList
    },
    // Circuit
    MU_MENU_YOGA_LIST (state, yogaList) {
        state.yogaList = yogaList.exList
    },
    // Select
    MU_MENU_SELECT_PROGRAM (state, selectProgram) {
        state.selectProgram = selectProgram
    },
    MU_MENU_SELECT_EXERCISE (state, selectExercise) {
        state.selectExercise = selectExercise
    },
    MU_MENU_SELECT_MENU (state, selectMenu) {
        state.selectMenu = selectMenu
    },
    // 운동결과
    MU_MENU_EX_RESULT_LIST (state, exResultList) {
        state.exResultList = exResultList
    },
}

const actions = {
    // program List
    async ACT_MENU_API_GET_EX_RESULT_LIST ( { commit }, exType) {
        const memAuthKey = localStorage.getItem('memAuthKey')
        let params = {
            memAuthKey: memAuthKey,
            exType: exType
        }
        let exResultList = await api.menu.API_GET_EX_RESULT_LIST(params)
        commit('MU_MENU_EX_RESULT_LIST', exResultList)
    },
    // Today
    async ACT_MENU_API_GET_TODAY_LIST ({ commit }, payload) {
        const memAuthKey = localStorage.getItem('memAuthKey')
        let params = {
            memAuthKey: memAuthKey,
            searchType: payload
        }
        let todayList = await api.menu.API_GET_TODAY_LIST(params)
        commit('MU_MENU_TODAY_LIST', todayList)
    },
    // Stretch
    async ACT_MENU_API_GET_STRETCH_EX_PROGRAM_LIST ({ commit }) {
        const memAuthKey = localStorage.getItem('memAuthKey')
        let params = {
            memAuthKey: memAuthKey,
            programType: 3              // 3 = 스트레칭
        }
        let stretchExProgramList = await api.menu.API_GET_STRETCH_EX_PROGRAM_LIST(params)
        commit('MU_MENU_STRETCH_EX_PROGRAM_LIST', stretchExProgramList)
    },
    async ACT_MENU_API_GET_STRETCH_EX_SORT_LIST ({ commit }) {
        const memAuthKey = localStorage.getItem('memAuthKey')
        let params = {
            memAuthKey: memAuthKey
        }
        let stretchExSortList = await api.menu.API_GET_STRETCH_EX_SORT_LIST(params)
        commit('MU_MENU_STRETCH_EX_SORT_LIST', stretchExSortList)
    },
    // Free Weight
    async ACT_MENU_API_GET_PART_LIST ({ commit }) {
        const memAuthKey = localStorage.getItem('memAuthKey')
        let params = {
            memAuthKey: memAuthKey,
            exType: 0
        }
        let freeWeightList = await api.menu.API_GET_EXERCISE_LIST(params)
        commit('MU_MENU_PART_LIST', freeWeightList)
    },
    async ACT_MENU_API_GET_PART_GUIDE ( Null, payload) {
        const memAuthKey = localStorage.getItem('memAuthKey')
        let params = {
            memAuthKey: memAuthKey,
            exSortCode: payload
        }
        let freeWeightGuide = await api.menu.API_GET_PART_GUIDE(params)
        return freeWeightGuide
    },
    async ACT_MENU_API_GET_PART_TOOL ( Null , payload) {
        return await api.menu.API_GET_PART_TOOL(payload)
    },
    // Circuit
    async ACT_MENU_API_GET_CIRCUIT_LIST ({ commit }) {
        const memAuthKey = localStorage.getItem('memAuthKey')
        let params = {
            memAuthKey: memAuthKey,
            exType: 5
        }
        let circuitList = await api.menu.API_GET_EXERCISE_LIST(params)
        commit('MU_MENU_CIRCUIT_LIST', circuitList)
    },
    async ACT_MENU_API_GET_CIRCUIT_PROGRAM ( Null , payload) {
        const memAuthKey = localStorage.getItem('memAuthKey')
        let params = {
            memAuthKey: memAuthKey,
            exSortCode: payload
        }
        return await api.menu.API_GET_CIRCUIT_PROGRAM(params)
    },
    async ACT_MENU_API_GET_CIRCUIT_PROGRAM_HOW_TO ( Null , payload) {
        const memAuthKey = localStorage.getItem('memAuthKey')
        let params = {
            memAuthKey: memAuthKey,
            exSortCode: payload
        }
        return await api.menu.API_GET_CIRCUIT_PROGRAM_HOW_TO(params)
    },
    async ACT_MENU_API_GET_CIRCUIT_GUIDE ( Null , payload) {
        const memAuthKey = localStorage.getItem('memAuthKey')
        let params = {
            memAuthKey: memAuthKey,
            exSortCode: payload
        }
        return await api.menu.API_GET_CIRCUIT_GUIDE(params)
    },
    async ACT_MENU_API_GET_YOGA_LIST ({ commit }) {
        const memAuthKey = localStorage.getItem('memAuthKey')
        let params = {
            memAuthKey: memAuthKey,
            exType: 9
        }
        let yogaList = await api.menu.API_GET_EXERCISE_LIST(params)
        commit('MU_MENU_YOGA_LIST', yogaList)
    },
    // Search Program
    async ACT_MENU_API_GET_PROGRAM_LIST ( Null , payload) {
        const memAuthKey = localStorage.getItem('memAuthKey')
        let params = {
            memAuthKey: memAuthKey,
            programNo: payload
        }
        return await api.menu.API_GET_PROGRAM_LIST(params)
    },
    // Select
    ACT_MENU_SELECT_PROGRAM({ commit }, payload){
        commit('MU_MENU_SELECT_PROGRAM', payload)
    },
    ACT_MENU_SELECT_EXERCISE({ commit }, payload){
        commit('MU_MENU_SELECT_EXERCISE', payload)
    },
    ACT_MENU_SELECT_MENU({ commit }, payload){
        commit('MU_MENU_SELECT_MENU', payload)
    },
}

const getters = {
    // Today
    GET_MENU_TODAY_LIST (state) {
        return state.todayList
    },
    // Stretch
    GET_MENU_STRETCH_LIST (state) {
        return state.stretchList
    },
    GET_MENU_STRETCH_EX_PROGRAM_LIST (state) {
        return state.stretchExProgramList
    },
    GET_MENU_STRETCH_EX_SORT_LIST (state) {
        return state.stretchExSortList
    },
    // Free Weight
    GET_MENU_PART_LIST (state) {
        return state.freeWeightList
    },
    // Circuit
    GET_MENU_CIRCUIT_LIST (state) {
        return state.circuitList
    },
    // Yoga
    GET_MENU_YOGA_LIST (state) {
        return state.yogaList
    },
    // Select
    GET_MENU_SELECT_PROGRAM (state) {
        return state.selectProgram
    },
    GET_MENU_SELECT_EXERCISE (state) {
        return state.selectExercise
    },
    GET_MENU_SELECT_MENU (state) {
        return state.selectMenu
    },
    // 운동결과
    GET_MENU_EX_RESULT_LIST (state) {
        return state.exResultList
    },
}

export default {
    state,
    mutations,
    actions,
    getters
}
