<template>
    <v-dialog
        v-model="dialog"
        persistent
    >
        <v-row class="next_exercise_time_area" align="center" justify="center">
            <v-col align="center" v-if="nextExerciseItems">
                <v-col 
                    v-if="nextExerciseItems.inputType === 'number'" 
                    class="next_exercise_time_description"
                >
                    <p>
                        <span :class="(GET_PART_PLAY_REST_TIME < 4)?'color_second':'white_second'">
                            {{GET_PART_PLAY_REST_TIME}}
                        </span> 초 후 다음으로 이동
                    </p>
                </v-col>
                <play-part-items-select-number
                    v-if="nextExerciseItems.inputType === 'number'"
                    :selectItems="nextExerciseItems"
                    v-on:close-dialog="CloseDialog(this)"
                />
                <play-part-items-select-weight
                    v-if="nextExerciseItems.inputType === 'weight'"
                    :selectItems="nextExerciseItems"
                    v-on:close-dialog="CloseDialog(this)"
                />
            </v-col>
        </v-row>
    </v-dialog>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'
import common from '@/mixins/common.js'

export default {
    name: 'NextExerciseTime',
    props:['nextExerciseItems','dialog'],
    mixins:[ common ],
    data:()=>({
    }),
    computed:{
        ...mapGetters([
            'GET_PART_PLAY_EXERCISE',
            'GET_PART_PLAY_REST_TIME',
        ])
    },
    watch:{
        GET_PART_PLAY_EXERCISE(value){
            if(value){
                this.CloseDialog(this)
            }
        },
        GET_PART_PLAY_REST_TIME(value){
            this.common_audio_start(value)
        }
    },
    mounted() {
    },
    methods: {
        ...mapMutations([
            'MU_PART_PLAY_EXERCISE',
        ]),
        CloseDialog(e){
            this.MU_PART_PLAY_EXERCISE(true)
            this.$emit('close-dialog', e)
        }
    },
}
</script>

<style scoped lang="scss">
.next_exercise_time_area{
    padding-top: 2rem !important;
    padding-bottom: 5rem !important;
    background-color: var(--primary-popup-bg);

    .next_exercise_time_description{
        color: white;
        font-size: var(--font-size-normal);
        margin: 6rem 0 3rem 0;
    }
    .next_exercise_time_description span{
        font-size: var(--font-size-large);
    }
}
</style>