<template>
    <v-row class="exercise_complete">
        <v-col align-self="center" align="center">
            <!-- skeleton mode -->
            <v-col v-if="GET_MENU_SELECT_EXERCISE.skeleton && GET_PART_PLAY_EXERCISE_MODE === 2">
                <v-btn 
                    color="#fff5a2"
                    width="100"
                    height="100" 
                    fab
                    @click="PushSkeleton()"
                >
                    <v-icon x-large>share</v-icon>
                </v-btn>
            </v-col>
            <!-- motion mode -->
            <v-row v-if="GET_PART_PLAY_EXERCISE_MODE === 2" align="center" justify="center" >
                <span class="exercise_complete_count">{{GET_PART_PLAY_PLAY_ITEMS.exerciseCount}}</span>
                <span class="exercise_complete_count">/ {{GET_PART_PLAY_GOAL_COUNT}}</span>
            </v-row>
            <!-- normal mode -->
            <v-row v-if="GET_PART_PLAY_EXERCISE_MODE === 1" align="center" justify="center" >
                <span :class="(GET_PART_PLAY_EXERCISE_TIME_MODE_TIME < 4)?'color_second':'white_second'" class="pr-10">
                    {{GET_PART_PLAY_EXERCISE_TIME_MODE_TIME}}
                </span>
            </v-row>
            <!-- normal mode -->
            <v-row v-if="GET_PART_PLAY_EXERCISE_MODE === 0" align="center" justify="center">
                <span class="exercise_complete_text">목표</span>
                <span class="exercise_complete_count">{{GET_PART_PLAY_GOAL_COUNT}}</span>
                <span class="exercise_complete_text">회</span>
            </v-row>
            <v-col>
                <v-col v-if="GET_PART_PLAY_EXERCISE_MODE === 0" class="exercise_complete_description">
                    {{GET_MENU_SELECT_EXERCISE.name}} {{GET_PART_PLAY_GOAL_COUNT}}회 진행후 <br>
                    운동완료 버튼을 누르세요
                </v-col>
                <v-btn 
                    v-if="GET_PART_PLAY_EXERCISE_MODE !== 1"
                    color="#fff5a2"
                    width="300"
                    height="120" 
                    class="exercise_complete_button"
                    @click="ExerciseSuccess()"
                >
                    운동 완료
                </v-btn>
            </v-col>
        </v-col>
        <play-part-items-next-exercise
            :dialog="nextExerciseItemsOn"
            :nextExerciseItems="nextExerciseItems"
            v-on:close-dialog="CloseDialog(this)"
        />
    </v-row>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'
import common from '@/mixins/common.js'

export default {
    name: 'ExerciseCompleteItems',
    mixins:[ common ],
    data:()=>({
        nextExerciseItemsOn: false,
        nextExerciseItems:{}
    }),
    computed:{
        ...mapGetters([
            'GET_MENU_SELECT_EXERCISE',
            'GET_PART_PLAY_PLAY_ITEMS',
            'GET_PART_PLAY_GOAL_COUNT',
            'GET_PART_PLAY_EXERCISE_MODE',
            'GET_PART_PLAY_EXERCISE_TIME_MODE_TIME',
        ]),
    },
    watch:{
        GET_PART_PLAY_EXERCISE_TIME_MODE_TIME(value){
            this.common_audio_start(value)
            if(value === 0) this.ExerciseSuccess()
        }
    },
    mounted() {
        this.EditItems()
    },
    methods: {
        ...mapMutations([
            'MU_PART_PLAY_EXERCISE',
        ]),
        CloseDialog(){
            this.nextExerciseItemsOn = false
        },
        EditItems(){
            this.MU_PART_PLAY_EXERCISE(true)
            // nextExercise
            this.nextExerciseItems = {
                inputType: 'number',
                title: this.GET_MENU_SELECT_EXERCISE.name,
                exerciseCount: 16,
            }
        },
        ExerciseSuccess(){
            this.$emit('close-exercise',this)
            this.nextExerciseItemsOn= true
            this.MU_PART_PLAY_EXERCISE(false)
        },
        PushSkeleton(){
            window.chrome.webview.postMessage('skeleton push')
            this.$emit('close-motion',this)
        }
    },
}
</script>

<style scoped lang="scss">
.exercise_complete{
    height: 65%;

    .exercise_complete_count{
        font-size: 7rem;
        font-style: italic;
        margin-right: 1rem;
    }
    .exercise_complete_text{
        font-size: var(--font-size-medium);
        padding-top: 3rem;
    }
    .exercise_complete_button{
        margin-top: 1rem;
        font-size: var(--font-size-medium);
        font-weight: 500;
    }
    .exercise_complete_description{
        color: #fff5a2;
        font-size: var(--font-size-small);
        padding-top: 1rem;
    }
}
</style>