<template>
    <v-row v-if="countItems" class="count_time_set" align="start">
        <v-col v-if="title" cols="12" class="mt-2">
            <span class="count_time_title">{{title}}</span>
        </v-col>
        <v-col cols="5" class="pt-1" align="start">
            <v-icon dark class="count_time_set mr-1 mb-2">apps</v-icon>
            <span>{{countItems.setCount}}/{{countItems.set}}셋트</span>
        </v-col>
        <v-col cols="4" class="pt-1" align="start">
            <v-icon dark class="count_time_set mr-1 mb-2">timer</v-icon>
            <span>{{GET_PART_PLAY_EXERCISE_TIME}}</span>
        </v-col>
        <v-col cols="3" v-if="countItems.weight" class="pt-1" align="start">
            <v-icon dark class="count_time_set mr-1 mb-2">fitness_center</v-icon>
            <span>{{countItems.weight}}kg</span>
        </v-col>
    </v-row>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
    name: 'CountTime',
    props:['title','countItems'],
    data:()=>({
    }),
    computed:{
        ...mapGetters([
            'GET_PART_PLAY_EXERCISE_TIME'
        ])
    },
    methods: {
        SelectSet (i) {
            let style = {}
            if(this.countItems.setCount === i){
                style = {
                    backgroundColor: '#fff5a2'
                }
            }
            return style
        },
    },
    mounted() {
    }
}
</script>

<style scoped lang="scss">
.count_time_btn{
    font-size: var(--font-size);
    font-style: italic;
    padding-right: 0.25rem;
    width: 3rem;
    height: 3rem;
}
.count_time_title{
    font-size: var(--font-size-normal);
}
.count_time_set{
    font-size: var(--font-size-medium);
}
</style>