    methods:{
        // track 경로
        exercise_create_ex_track(exCode){
            let track
            let fileList = require.context('@/assets/track/ex',false,/\.vtt$/)
            fileList.keys().forEach(element => {
                const name = element.replace(/(\.\/|\.vtt)/g, '')
                if( name == String(exCode) ){
                    track = require(`@/assets/track/ex/${exCode}.vtt`)
                }
            })
            return track
        },
        exercise_create_rest_track(exCode){
            let track
            let fileList = require.context('@/assets/track/rest',false,/\.vtt$/)
            fileList.keys().forEach(element => {
                const name = element.replace(/(\.\/|\.vtt)/g, '')
                if( name == String(exCode) ){
                    track = require(`@/assets/track/rest/${exCode}.vtt` )
                }
            })
            return track
        },
        // audio 경로
        exercise_create_rest_audio(exCode){
            let audio
            let fileList

            fileList = require.context('@/assets/audio/rest',false,/\.wav$/)
            fileList.keys().forEach(element => {
                const name = element.replace(/(\.\/|\.wav)/g, '')
                if( name == String(exCode) ){
                    audio = require(`@/assets/audio/rest/${exCode}.wav`)
                }
            })
            return audio
        },
        // video 경로
        exercise_create_ex_video(exCode){
            // let video = `local-resource://videos/ex/${exCode}.mp4` // public path 경로
            let video
            let fileList

            fileList = require.context('@/assets/videos/ex',false,/\.mp4$/)
            fileList.keys().forEach(element => {
                const name = element.replace(/(\.\/|\.mp4)/g, '')
                if( name == String(exCode) ){
                    video = require(`@/assets/videos/ex/${exCode}.mp4`)
                }
            })
            return video
        },
        exercise_create_rest_video(exCode){
            // let video = `local-resource://videos/rest/${exCode}.mp4` // public path 경로            
            let video
            let fileList

            fileList = require.context('@/assets/videos/rest',false,/\.mp4$/)
            fileList.keys().forEach(element => {
                const name = element.replace(/(\.\/|\.mp4)/g, '')
                if( name == String(exCode) ){
                    video = require(`@/assets/videos/rest/${exCode}.mp4`)
                }
            })
            return video
        },
        // image 경로
        exercise_create_image(exCode){
            let image
            let fileList = require.context(`@/assets/images/thumbnail`,false,/\.jpg$/)
            fileList.keys().forEach(element => {
                const name = element.replace(/(\.\/|\.jpg)/g, '')
                if( name == exCode ){
                    image = require(`@/assets/images/thumbnail/${exCode}.jpg`)
                }
            })
            // 이미지가 없는 경우
            if(image === undefined){
                image = require(`@/assets/images/thumbnail/none.jpg`)
            }
            return image
        }
    }
